// Call plugin.
import { callSubscribe, callUnsubscribe } from '../requests/subscriptions'

// Other plugins.
import { reportSubscriptionFinished, reportUnsubscriptionFinished } from '../../../subscription/interface/actions'
import { getNotificationChannels } from '../../../subscription/interface/selectors'
import { getRequestInfo } from '../../../auth/interface/selectors'
import { logManager } from '../../../logs'

// Helpers.
import { platforms } from '../../../constants'

// Libraries.
import { call, put, select } from 'redux-saga/effects'

const log = logManager.getLogger('CALL')

/**
 * Subscription saga. Creates a new call subscription channel.
 * @method callSubscribe
 * @param {Object} config   The subscription request config
 * @param {string} type     The type of notification channel for this subscription.
 */
export function * subscribe (config, type) {
  const requestInfo = yield select(getRequestInfo, platforms.CPAAS)
  const channels = yield select(getNotificationChannels)
  const channelInfo = channels.notificationChannels[type]

  const requestOptions = {}
  requestOptions.method = 'POST'
  requestOptions.url =
    `${requestInfo.baseURL}` +
    `/cpaas/webrtcsignaling/${requestInfo.version}/${requestInfo.username}` +
    '/subscriptions'

  log.debug(`Subscribing to call service on ${type} channel.`)
  const response = yield call(callSubscribe, requestInfo, channelInfo)

  yield put(
    reportSubscriptionFinished({
      ...response,
      service: 'call',
      type: type
    })
  )
}

/**
 * Unsubscription saga. Deletes an existing call subscription.
 * @method unsubscribe
 */
export function * unsubscribe (action) {
  const { subscriptions } = yield select(getNotificationChannels)
  let callSubscription = subscriptions.filter(sub => {
    return sub.service === 'call' && sub.channelType === action.payload.type
  })

  if (callSubscription.length === 0) {
    // "Error" scenario: Can't unsubscribe to a service
    //    that we don't have.
  } else if (callSubscription.length > 1) {
    // "Error" scenario: There shouldn't be able to have
    //    more than 1 of the same service.
  } else {
    callSubscription = callSubscription[0]
  }

  log.info(`Unsubscribing from call service on ${action.payload.type} channel.`)
  const requestInfo = yield select(getRequestInfo, platforms.CPAAS)

  const response = yield call(callUnsubscribe, requestInfo, callSubscription)

  yield put(
    reportUnsubscriptionFinished({
      ...response,
      service: 'call',
      type: action.payload.type
    })
  )
}
