// Subscription plugin.
import * as actionTypes from '../../interface/actionTypes'
import * as actions from '../../interface/actions'

import { ensureChannelOpen, closeChannel } from './channels'

import {
  getRegisteredServices,
  getSubscribedServices,
  getNotificationChannels,
  getSubscriptionConfig
} from '../../interface/selectors'

// Other plugins
import * as connectivityActionTypes from '../../../connectivity/interface/actionTypes'
import { DISCONNECT_REASONS } from '../../../auth/constants'
import { getUserInfo } from '../../../auth/interface/selectors'

// Helpers.
import { logManager } from '../../../logs'
import BasicError, { subscriptionCodes } from '../../../errors'
import { waitFor } from '../../../common/effects'

// Libraries.
import { call, put, select, take, actionChannel, delay, takeEvery } from 'redux-saga/effects'
import { intersection, compact } from 'lodash/fp'

// Constants
import { platforms } from '../../../constants'

const platform = platforms.CPAAS
const log = logManager.getLogger('SUBSCRIPTION')

/**
 * Entry point for ALL subscription change actions.
 * Queues all subscription changes (ie. subscribe, unsubscribe) to avoid async
 * scenarios. This ensures channels and subscriptions are not duplicated
 * and that resources are created/clean-up when needed.
 * @method subscriptionFlow
 */
export function * subscriptionFlow () {
  // Use a channel to queue ALL subscription change requests.
  const subscriptionChannel = yield actionChannel([actionTypes.SUBSCRIBE, actionTypes.UNSUBSCRIBE])

  while (true) {
    const action = yield take(subscriptionChannel)

    // Get the channel information of the channel to be closed.
    const { notificationChannels } = yield select(getNotificationChannels)
    const channel = notificationChannels[action.payload.type]

    // Handle the action appropriately.
    if (action.type === actionTypes.SUBSCRIBE) {
      if (!action.payload.services || action.payload.services.length === 0) {
        // Error scenario: No services specified.
        // TODO: Better error.
        log.info('Failed subscription: No services specified.')
        yield put(actions.subscribeFinished({ error: true }))
        continue
      }

      // Open the notification channel if it isn't already.
      const result = yield call(ensureChannelOpen, action.payload.type)
      if (!result.success) {
        log.info('Failed to open channel. Error: ', result.error)
        // Error scenario: Could not open channel.
        yield put(actions.subscribeFinished({ error: result.error }))
        continue
      }

      // Ensure only one user credentials can be used at a time.
      // If user of SDK instance triggers a subscribe for services
      // using another set of credentials, then it needs to unsubscribe
      // from currently subscribed services.
      const currentUserInfo = yield select(getUserInfo)
      let currentSubscribedUsername
      if (channel && channel.resourceURL) {
        currentSubscribedUsername = channel.resourceURL
          .split('/channels/')[0]
          .split('/')
          .pop()
      }

      if (currentSubscribedUsername && currentUserInfo.username !== currentSubscribedUsername) {
        // See NOTE below, for the same yield delay(0)
        yield delay(0)

        // Error scenario: When attempting to subscribe,
        // if we detect that there was a previous subscribed user,
        // then that currently subscribed user
        // has to be the same as the currently authenticated user.
        yield put(
          actions.subscribeFinished({
            error: new BasicError({
              message:
                'Previously subscribed username does not match the currently authenticated username. Unsubscribe previous user from service(s).',
              code: subscriptionCodes.CPAAS_SERVICE_SUB_FAIL
            })
          })
        )
        continue
      }

      log.info('Subscribing for services.')
      // Subscribe to the services and retrieve a list of their responses.
      const subResponses = yield call(subscribeForServices, action)

      // Report that the subscription flow has finished.
      if (subResponses.error) {
        log.info('Failed to subscribe for services. Error: ', subResponses.error)
        yield put(
          actions.subscribeFinished({
            error: subResponses.error,
            type: action.payload.type
          })
        )
      } else {
        log.info('Successfully subscribed for services.')
        yield put(
          actions.subscribeFinished({
            type: action.payload.type
          })
        )
      }
    } else {
      // TODO: How to do a "unsubscribe all" services?
      // ie. not need the application to specify them all individually.
      // Might be simpler for the API to be sub/unsub(type, services).

      // Unsubscribe the services.
      const unsubResponses = yield call(unsubscribeServices, action)
      if (unsubResponses.error) {
        log.info('Failed to unsubscribe from services. Error: ', unsubResponses.error)
        yield put(
          actions.unsubscribeFinished({
            error: unsubResponses.error,
            type: action.payload.type
          })
        )
        continue
      }
      log.info('Successfully unsubscribed from services.')

      // If there are no longer any subscribed services for this channel (and
      //    the channel is open), close it.
      const subscribedServices = yield select(getSubscribedServices, action.payload.type)
      if (subscribedServices.length === 0 && channel) {
        const closeResponse = yield call(closeChannel, channel, platform)

        if (closeResponse.error) {
          // TODO: Error scenario?
          log.info('Failed to close channel. Error: ', closeResponse.error)
        } else {
          // TODO: Parameters.
          yield put(actions.channelClosed(action.payload.type))
        }
      }

      // Report that the unsubscription flow has finished.
      yield put(
        actions.unsubscribeFinished({
          type: action.payload.type
        })
      )
    }
  }
}

/**
 * This function handles the flow of subscribing for CPaaS services.
 * This means it dispatches a command action to tell other plugins to subscribe.
 * Once all plugins have done their subscription (if they need one), we return
 * the subscription response information.
 * @method subscribeForServices
 * @param  {Object} action A subscription action.
 * @param  {Object} action.payload The action's payload
 * @param  {string} action.payload.type The type of notification channel for this subscription.
 * @param  {Array<Object>} action.payload.services The requested services for this subscription
 * @return {Object}
 */
function * subscribeForServices (action) {
  const requestedServices = action.payload.services

  // Get lists of registered and subscribed services
  const registeredServices = yield select(getRegisteredServices)
  const subscribedServices = yield select(getSubscribedServices, action.payload.type)

  // Filter requested services to remove invalid services (services not registered / already subscribed to)
  const notRegistered = []
  const alreadySubscribed = []
  const validServices = requestedServices.filter(serviceConfig => {
    // Remove requested services of plugins that are not registered
    if (!registeredServices.includes(serviceConfig.service)) {
      notRegistered.push(serviceConfig.service)
      return false
      // Remove requested services already subscribed to
    } else if (subscribedServices.includes(serviceConfig.service)) {
      alreadySubscribed.push(serviceConfig.service)
      return false
    }
    // Valid requested service
    return true
  })

  let reason
  if (notRegistered.length > 0) {
    reason = 'Not registered for requested service(s): ' + notRegistered + '.'
    log.debug(reason)
  }
  if (alreadySubscribed.length > 0) {
    reason = 'Already subscribed for requested service(s): ' + alreadySubscribed + '.'
    log.debug(reason)
  }

  if (validServices.length === 0) {
    // eslint-disable-next-line no-warning-comments
    /**
     * While odd looking, delay(0) is needed to let the "subscribe action" finish its flow
     * before the "subscribe finish" action is dispatched.
     * For a very in-depth explanation, see comments on this PR:
     * <https://github.com/Kandy-IO/Kandy.js/pull/2579#issuecomment-589670097>
     */
    yield delay(0)

    // TODO: Proper error / return.
    return {
      error: new BasicError({
        message: 'Failed to subscribe: No valid services provided. Reason: ' + reason,
        // TODO: Proper error code.
        code: 'BAD_INPUT'
      })
    }
  }
  log.info(`Requesting subscriptions for services: ${validServices.map(serviceConfig => serviceConfig.service)}.`)

  // Announce to all plugins that subscriptions should happen.
  yield put(actions.doPluginSubscriptions(validServices, action.payload.type))

  // What we're waiting for.
  const waitPatterns = validServices.map(serviceConfig => action =>
    action.type === actionTypes.PLUGIN_SUBSCRIPTION_FINISHED && action.payload.service === serviceConfig.service
  )

  const subscriptionConfig = yield select(getSubscriptionConfig, platform)
  // Wait for a response from each plugin.
  const { results } = yield waitFor(subscriptionConfig.timeout * 1000, waitPatterns)

  // TODO: Would it be better to handle partial results here?
  return compact(results)
}

/**
 * This function handles the flow of unsubscribing for CPaaS services.
 * This means it dispatches a command action to tell other plugins to unsubscribe.
 * Once all plugins have done their unsubscription (if they need one), we return
 * the unsubscription response information.
 * The logic in this saga is a copy from the `subscribeForServices` saga.
 * @method unsubscribeServices
 * @param  {Object} action An unsubscription action.
 * @return {Object}
 */
function * unsubscribeServices (action) {
  const subscribedServices = yield select(getSubscribedServices, action.payload.type)

  // Get the list of services we want to unsubscribe, but aren't subscribed.
  const notSubscribed = action.payload.services.filter(service => {
    return subscribedServices.indexOf(service) === -1
  })
  if (notSubscribed.length > 0) {
    log.debug(`Not subscribed for services for unsubscription: ${notSubscribed}.`)
  }

  // Get the list of subscribed services we want to unsubscribe.
  const validServices = intersection(subscribedServices, action.payload.services)
  log.info(`Unsubscribing from services: ${validServices}.`)

  if (validServices.length === 0) {
    // TODO: Proper error / return.
    return {
      error: new BasicError({
        message: 'Failed to unsubscribe: No valid services provided.',
        // TODO: Proper error code.
        code: 'BAD_INPUT'
      })
    }
  }

  const subscriptionConfig = yield select(getSubscriptionConfig, platform)

  // Announce to all plugins that unsubscriptions should happen
  yield put(actions.doPluginUnsubscriptions(validServices, action.payload.type))

  // What we're waiting for.
  const waitPatterns = validServices.map(service => action =>
    action.type === actionTypes.PLUGIN_UNSUBSCRIPTION_FINISHED && action.payload.service === service
  )

  // Wait for a response from each plugin.
  const { results } = yield waitFor(subscriptionConfig.timeout * 1000, waitPatterns)

  // Get the service from each of the unsubscribed actions
  let resultServices = results
  if (Array.isArray(results)) {
    resultServices = results.map(resultAction => resultAction.payload.service)
  }

  return compact(resultServices)
}

/**
 * Triggers onConnectionLost saga when a WS_RECONNECT_FAILED actionType occurs
 * @method onConnectionLostEntry
 */
export function * onConnectionLostEntry () {
  yield takeEvery(connectivityActionTypes.WS_RECONNECT_FAILED, onConnectionLost)
}

/**
 * Handles lost connections from the connectivity plugin
 * @method onConnectionLost
 */
export function * onConnectionLost () {
  yield put(
    actions.unsubscribeFinished({
      reason: DISCONNECT_REASONS.LOST_CONNECTION
    })
  )
}
