// Subscription plugin.
import { registerService as registerServiceAction } from './actions'
import { PLUGIN_SUBSCRIPTION, PLUGIN_UNSUBSCRIPTION } from './actionTypes'

// Helpers.
import { logManager } from '../../logs'

// Libraries.
import { put, takeEvery, call } from 'redux-saga/effects'
import { isFunction } from 'lodash/fp'

const log = logManager.getLogger('SUBSCRIPTION')

/**
 * Effect for plugins to register a service with the subscription plugin.
 * @method registerService
 * @param  {string} service The name of the service.
 * @param  {Saga} subscribe The saga to be called when subscription is requested.
 * @param  {Saga} unsubscribe The saga to be called when unsubscription is requested.
 */
export function * registerService (service, subscribeSaga, unsubscribeSaga) {
  // "Register" the service with the subscription plugin, so it knows a plugin
  //    has subscription/unsubscription logic for it.
  yield put(registerServiceAction(service))

  // Redux-saga take() pattern.
  function serviceSubscribePattern (action) {
    if (action.type === PLUGIN_SUBSCRIPTION) {
      return action.payload.services.some(serviceConfig => {
        return serviceConfig.service === service
      })
    }
  }
  // Redux-saga take() pattern.
  function serviceUnsubscribePattern (action) {
    return action.type === PLUGIN_UNSUBSCRIPTION && action.payload.services.indexOf(service) !== -1
  }

  // Intermediate saga to extract and pass only relevant service config from the payload to the subscribe saga
  function * extractConfigAndSubscribe (action) {
    const serviceConfig = action.payload.services.find(serviceConfig => serviceConfig.service === service)
    yield call(subscribeSaga, serviceConfig, action.payload.type)
  }

  // Ensure that `takeEvery` is only called when an actual function/saga is provided.
  //    Otherwise it may cause infinite loop error issues.
  if (isFunction(subscribeSaga)) {
    // Perform subscription logic when triggered by the action.
    yield takeEvery(serviceSubscribePattern, extractConfigAndSubscribe)
  } else {
    log.error(`Registering ${service} service without subscribe functionality.`)
  }

  if (isFunction(unsubscribeSaga)) {
    // Perform unsubscription logic when triggered by the action.
    yield takeEvery(serviceUnsubscribePattern, unsubscribeSaga)
  } else {
    log.error(`Registering ${service} service without unsubscribe functionality.`)
  }
}
