import { cloneDeep } from 'lodash/fp'
/**
 * Retrieves all the groups in state.
 * @method getAllGroups
 * @param  {Object} state Redux state.
 * @return {[Object]}  List of groups.
 */
export function getAllGroups (state) {
  return cloneDeep(state.groups.groups)
}

/**
 * Retrieves group information for a specified group in state.
 * @method get
 * @param  {Object} state Redux state.
 * @return {Object}  Group information of specified group.
 */
export function getGroup (state, groupId) {
  const group = state.groups.groups.find(group => group.id === groupId)
  if (group) {
    return cloneDeep(group)
  }
}

/**
 * Retrieves list of participants from a specified group in state.
 * @method getParticipants
 * @param  {Object} state Redux state.
 * @return {[Object]}  List of participants in specified group.
 */
export function getParticipants (state, groupId) {
  const group = state.groups.groups.find(group => group.id === groupId)
  if (group) {
    return cloneDeep(group.participants)
  }
}

/**
 * Retrieves all the invitations in state.
 * @method getInvitations
 * @param  {Object} state Redux state.
 * @return {[Object]}  List of invitations.
 */
export function getInvitations (state, groupId) {
  return cloneDeep(state.groups.invitations)
}
