/**
 * A Conversation object represents a conversation either between two users, or between a
 * user and a group. A user can create a Conversation using {@link conversation.create} Messaging API.
 *
 * A Conversation can be used to create messages to send using the Conversation and Messaging APIs
 * {@link conversation.Conversation.createMessage Conversation.createMessage} and {@link conversation.Message.send Message.send} functions.
 *
 *
 * Once a sender sends the initial message (within a conversation) to a recipient, there will be a
 * conversation object saved in both sender and recipient's state.
 *
 * @property {string} destination The ID/group ID of the remote user with which the current user is having a conversation.
 * @property {string} address The ID of the current user who is having a conversation.
 * @property {number} lastReceived The timestamp (milliseconds since epoch) of when a message was last received in this conversation.
 * This property applies only to conversation object stored in recipient's state.
 * @property {string} type The type of conversation. See {@link conversation.chatTypes} for valid types.
 * @property {string} lastMessage The last message received.
 * @property {Array<conversation.Message>} messages The array of message objects.
 * @property {Array<string>} isTypingList The array identifying the User IDs of the users who are currently typing.
 *
 * @public
 * @static
 * @typedef {Object} Conversation
 * @memberof conversation
 */
// `features` and `lastPull` are not documented because they're intended to be internal

/**
 * A Part is a custom object representing a section of the payload of a message. Messages can have one or many Parts.
 *
 * @property {string} type The payload type. Can be `text`, `json`, or `file`.
 * @property {string} text The text of the message. Messages with file or json attachments are still required to have text associated to it.
 * @property {Object} [json] The object corresponding to a json object to attach to a message. A `Part` cannot have both json and a file.
 * @property {File} [file] The file to attach to a message. A `Part` cannot have both json and a file.
 *
 * @public
 * @static
 * @typedef {Object} Part
 * @memberof conversation
 */

/**
 * A Message object is a means by which a sender can deliver information to a recipient.
 *
 * Creating and sending a message:
 *
 * The message object can be obtained through the {@link conversation.Conversation.createMessage Conversation.createMessage} API on an existing conversation.
 *
 * Messages have Parts which represent pieces of a message, such as a text part, a json object part or a file part.
 * Once all the desired parts have been added to the message using the {@link conversation.Message.addPart Message.addPart} function,
 * the message can then be sent using the {@link conversation.Message.send Message.send} function.
 *
 * Once the sender sends a message, this message is saved in sender's state as an object.
 * Similarly, once the recipient gets a message, this message is saved in recipient's state.
 *
 * Retrieving a delivered message:
 *
 * Once a message is delivered successfully, it can be
 * obtained through the {@link conversation.Conversation.getMessages Conversation.getMessages} or {@link conversation.Conversation.getMessage Conversation.getMessage} API on an existing conversation.
 *
 * Below are the properties pertaining to the message object, returned by Conversation.getMessage(s) APIs, for either sender or recipient.
 *
 * @property {number} timestamp A Unix timestamp in seconds marking the time when the message was created by sender.
 * @property {Array<conversation.Part>} parts An array of Part Objects.
 * @property {string} sender The primary contact address of the sender.
 * @property {Array<string>} destination An array of primary contact addresses associated with various destinations to which the message is meant to be delivered.
 * @property {string} messageId The unique id of the message. The message object (stored in sender's state) has a different id
 * than the one associated with the message object stored in recipient's state.
 * @property {string} type The type of message that was sent. See {@link conversation.chatTypes} for valid types.
 * This property applies only to message objects stored in sender's state.
 * @property {boolean} isFetchingLinks Whether or not the recipient of the message is in the process of fetching the message attachment(s) using the provided link(s).
 * @public
 * @static
 * @typedef {Object} Message
 * @memberof conversation
 */

import { find, includes, isEqual } from 'lodash/fp'
import { convoActions, messageActions } from './actions'
import { chatTypes } from '../../mappings'

import { getMessagingConfig, findConversation } from './selectors'
import { getUserInfo } from '../../../auth/interface/selectors'
import compose from '@stamp/compose'
// Events
import { emitEvent } from '../../../events/interface/actions'
import { MESSAGES_ERROR } from './eventTypes'

import { logManager } from '../../../logs'
const log = logManager.getLogger('MESSAGING')

/**
 * Base conversation stamp
 *
 * @param {Array<string>} destination The destination(s) for messages being sent through
 * this conversation in this instance of the SDK. This should be an Array with any number of user IDs.
 * @param {string} [type='chat-oneToOne'] The message type. See {@link conversation.chatTypes} for valid types.
 * @param {string} [id=undefined] id The unique identifier for the conversation.
 * @param {string} [description=''] The description associated with the conversation.
 * @param {Array<conversation.Message>} [messages=[]] An array containing the conversation's messages.
 * @param {Array<string>} [isTypingList=[]] A array that represents the list of users who are currently typing.
 * @param {number} lastReceived The timestamp associated with the last received message.
 */
const conversationBase = {
  initializers: [
    function ({
      destination,
      type = chatTypes.ONETOONE,
      id = undefined,
      description = '',
      messages = [],
      isTypingList = [],
      lastReceived,
      lastPull
    }) {
      this.destination = destination
      this.type = type
      this.description = description
      this.messages = messages
      this.isTypingList = isTypingList
      this.id = id
      const features = getMessagingConfig(this.context.getState()).features
      if (this.destination.length === 1) {
        const groupIndex = features.indexOf('group')
        if (groupIndex > -1) {
          features.splice(groupIndex, 1)
        }
      }
      this.features = features
      this.lastReceived = lastReceived
      this.lastPull = lastPull
      this.isPending = false
    }
  ],

  methods: {
    /**
     * Creates and return a message object. You must specify a part. If this is a simple text message, provide a `text` part as demonstrated in the example below.
     *
     * If successful, the event {@link conversation.event:messages:change messages:change} will be emitted.
     *
     * @public
     * @static
     * @memberof conversation.Conversation
     * @method createMessage
     * @constructs Message
     * @param {conversation.Part} part The Part to add to the message.
     * @returns {conversation.Message} The newly created Message object.
     *
     * @example
     * conversation.createMessage({type: 'text', text: 'This is the message'});
     */
    createMessage (part) {
      if (typeof part === 'string') {
        part = { type: 'text', text: part }
      }
      const parts = Array.isArray(part) ? part : [part]

      const messageContext = {
        features: this.features,
        isGroup: this.destination.length > 1,
        id: this.id,
        type: this.type
      }
      return this.context.primitives.Message({
        destination: this.destination,
        parts: parts,
        context: messageContext,
        type: this.type
      })
    },

    /**
     * Clears all messages in this conversation from local state.
     *
     * If successful, the event {@link conversation.event:messages:change messages:change} will be emitted.
     *
     * @public
     * @static
     * @memberof conversation.Conversation
     * @method clearMessages
     */
    clearMessages () {
      this.context.dispatch(messageActions.clearMessages(this.destination, this.type))
    },

    /**
     * Gets all messages from this conversation.
     *
     * @public
     * @static
     * @memberof conversation.Conversation
     * @method getMessages
     * @return {Array<conversation.Message>} An array of messages.
     */
    getMessages: function () {
      const conversation = findConversation(this.context.getState(), this.destination, this.type)

      return conversation.messages.map(message => {
        if (!message.parts) {
          log.debug('no message parts found on message, skipping message')
          return
        }
        return this.getMessage(message.messageId)
      })
    },

    /**
     * Gets a specific message from this conversation.
     *
     * @public
     * @static
     * @method getMessage
     * @memberof conversation.Conversation
     * @param {string} messageId The ID of the message to retrieve.
     * @return {conversation.Message} A message object.
     */
    getMessage (messageId) {
      const convo = findConversation(this.context.getState(), this.destination, this.type)
      const message = find(message => message.messageId === messageId)(convo.messages)

      if (!message) {
        log.debug(`Message (${messageId}) not found in conversation (${this.destination}).`)
        return
      }

      const { parts, sender, timestamp, isFetchingLinks } = message
      const { type, features, destination, id } = this
      const mixinMessage = this.context.primitives.Message({
        parts,
        sender,
        timestamp,
        isFetchingLinks,
        destination,
        type,
        messageId,
        context: {
          features,
          id,
          type,
          isGroup: destination.length > 1
        }
      })

      // TODO: Have these functions as proper mixin functions, instead of adding them manually afterwards.
      mixinMessage.forward = participant => {
        this.context.dispatch(
          messageActions.sendMessage(participant, mixinMessage.parts, Date.now(), this.type, this.id)
        )
      }
      // Only allow the end user to markRead on message that were incoming.
      const userInfo = getUserInfo(this.context.getState())
      if (mixinMessage.sender !== userInfo.username) {
        // TODO: Have these functions as proper mixin functions, instead of adding them manually afterwards.
        mixinMessage.markRead = () => {
          this.context.dispatch(messageActions.sendMessageRead(mixinMessage.messageId, this.destination))
        }
      }
      return mixinMessage
    },

    /**
     * Deletes specified messages from this conversation.
     * Provide an array of message IDs for the messages to be deleted.
     *
     * If successful, the event {@link conversation.event:messages:change messages:change} will be emitted.
     *
     * @public
     * @static
     * @memberof conversation.Conversation
     * @method deleteMessages
     * @param {Array<string>} [messageIds=[]] An array of message IDs for the messages to be deleted
     */
    deleteMessages: function (messageIds = []) {
      messageIds.forEach(messageId => {
        this.context.dispatch(messageActions.deleteMessage(this.id, this.destination, this.type, messageId))
      })
    },

    /**
     * Delete the conversation.
     *
     * If successful, the event {@link conversation.event:messages:change messages:change} will be emitted.
     *
     * @public
     * @static
     * @memberof conversation.Conversation
     * @method delete
     */
    delete: function () {
      this.context.dispatch(convoActions.deleteConversation(this.id, this.destination, this.type))
    },

    /**
     * Subscribe to this conversation's messages list to receive notification of changes.
     *
     * @public
     * @static
     * @memberof conversation.Conversation
     * @param {Function} subscriber A subscriber function to be triggered when the messages array of this conversation is updated.
     * @param {string} subscriber.destination The conversation participants.
     * @param {string} subscriber.messageId The ID of the message that caused the event.
     * @return {Function} The unsubscribe function.
     */
    subscribe: function (subscriber) {
      if (subscriber) {
        // Create a subscriber wrapper to properly determine if this messages:change event is relevant to this convo
        // TODO: we might need to change the name of `conversationId` as it is used here. Conversations have actual IDs in the backend, which are an integer value rather than an array of strings, as is the case with destination. We track conversation IDs, as they exist in the back end, as we are required to identify the conversation by `id` when performing actions in the backend on a Group Conversation.
        const subscriberWrapper = ({ destination, messageId }) => {
          if (isEqual(destination, this.destination)) {
            subscriber({ destination, messageId })
          }
        }
        // Subscribe to the messages:change event with the wrapped subscriber
        this.context.api.on('messages:change', subscriberWrapper)

        // Return the unsubscribe function
        return () => {
          this.context.api.off('messages:change', subscriberWrapper)
        }
      }
    }
  }
}

/*
 * Conversation history stamp. Handles any functions that retrieve history from the server concerning conversations.
 */
const conversationHistory = {
  initializers: [
    function ({ lastMessage }) {
      this.lastMessage = lastMessage
      const features = getMessagingConfig(this.context.getState()).features
      if (!includes('history', features)) {
        const prototype = Object.getPrototypeOf(this)
        delete prototype.fetchMessages
      }
      return this
    }
  ],
  methods: {
    /**
     * Allows the user to fetch messages associated with a specific conversation to make them available.
     * When the operation is complete, a `messages:change` event will be emitted.
     * Messages can then be retrieved using {@link conversation.Conversation.getMessages Conversation.getMessages}.
     *
     * If successful, the event {@link conversation.event:messages:change messages:change} will be emitted.
     *
     * @public
     * @static
     * @memberof conversation.Conversation
     * @method fetchMessages
     * @param {number} [amount=50] An amount of messages to fetch.
     */
    fetchMessages: function (amount = 50) {
      this.context.dispatch(messageActions.fetchMessages(this.id, this.destination, amount, this.type))
    }
  }
}
/*
 * Conversation is Typing. Responsible for updating the state of who is idling or actively typing.
 */
const conversationIsTyping = {
  initializers: [
    function () {
      const features = getMessagingConfig(this.context.getState()).features
      if (!includes('isTyping', features)) {
        const prototype = Object.getPrototypeOf(this)
        delete prototype.setIsTyping
      }
      return this
    }
  ],
  methods: {
    /**
     * Sets the typing status of the conversation for the current user.
     * Other participants will be notified of changes to the conversation's typing status.
     *
     * If successful, the event {@link conversation.event:isTypingList:change isTypingList:change} will be emitted.
     *
     * @public
     * @static
     * @memberof conversation.Conversation
     * @method setIsTyping
     * @param {boolean} isTyping Whether the user is typing or not
     */
    setIsTyping: function (isTyping) {
      const typingState = isTyping ? 'active' : 'idle'
      this.context.dispatch(
        convoActions.setIsTyping({ state: typingState, destination: this.destination, type: this.type })
      )
    }
  }
}

/*
 * Base Message stamp
 * @param {string} destination The Destination for messages being sent through
 * this conversation in this instance of the SDK. This can be one or many users,
 * separated by commas `,`
 * @param  {Object} parts Initial part to the message.
 * @param  {Object} context Information and capabilities for how the message will act with regard to the conversation.
 * @param  {Array} context.features Array of features the conversation supports.
 * @param  {string} type The type of conversation ('chat-onToOne', 'chat-group' or 'sms')
 * @param  {Function} context.send Function for sending the message.
 * @param  {number} timestamp unix timestamp in seconds.
 * @param  {boolean} isFetchingLinks Whether or not the recipient of the message is in the process of fetching the message attachment(s) using the provided link(s).
 * @param  {string} sender The author of the message.
 * @param  {string} messageId A unique ID for looking up the message.
 * @param  {string} [type='chat-oneToOne'] The message type. See {@link conversation.chatTypes} for valid types.
 */
const messageBase = {
  initializers: [
    function ({
      destination,
      parts,
      context,
      timestamp,
      isFetchingLinks,
      sender,
      messageId,
      type = chatTypes.ONETOONE
    }) {
      this.destination = Array.isArray(destination) ? destination : [destination]
      this.convoContext = context
      this.type = type
      this.messageId = messageId
      this.sender = sender
      this.timestamp = timestamp
      this.isFetchingLinks = isFetchingLinks
      this.parts = parts
    }
  ],
  methods: {
    /**
     * Sends the message.
     *
     * @public
     * @static
     * @method send
     * @memberof conversation.Message
     */
    send () {
      log.debug('Send message', this)
      const dateInSeconds = (Date.now() / 1000) | 0
      this.context.dispatch(
        messageActions.sendMessage(this.destination, this.parts, dateInSeconds, this.type, this.convoContext.id)
      )
    }
  }
}

/**
 * stamp to add message parts capabilities to a Message primitive
 * @name withParts
 * @param  {Object} context - Information and capabilities for how the message will act with regard to the conversation.
 * @param  {Array<string>} context.features - Array of features the conversation supports.
 */
const withParts = {
  initializers: [
    function ({ context: { features = [] } }) {
      if (!includes('parts', features)) {
        const prototype = Object.getPrototypeOf(this)
        delete prototype.addPart
      }
      return this
    }
  ],
  methods: {
    /**
     * Add an additional `Part` to a message.
     *
     * @public
     * @static
     * @memberof conversation.Message
     * @param {conversation.Part} part The `Part` to add to the message.
     */
    addPart (part) {
      // Validate the part. If not valid, returns an error.
      const validationResponse = validatePart(part, this.convoContext.features)
      if (validationResponse instanceof Error) {
        this.context.dispatch(emitEvent(MESSAGES_ERROR, { error: validationResponse.message }))
      }
      this.parts.push(part)
    }
  }
}

/**
 * stamp to allow creation of multimedia links
 * @name multimediaLinks
 * @param  {Object} context - Information and capabilities for how the message will act with regard to the conversation.
 * @param  {Array<string>} context.features - Array of features the conversation supports.
 */
const multimediaLinks = {
  initializers: [
    function ({ context: { features = [] } }) {
      if (!includes('rich', features)) {
        const prototype = Object.getPrototypeOf(this)
        delete prototype.createImageLinks
      }
      return this
    }
  ],
  methods: {
    /**
     * Creates a usable image link for the message in this `Message` instance.
     *
     * @public
     * @static
     * @memberof conversation.Message
     */
    createImageLinks () {
      const { parts, destination, type, messageId } = this
      this.context.dispatch(messageActions.getImageLinks({ parts, destination, type, messageId }))
    }
  }
}

/*
 * A helper function to validate inputs. Will be progressively updated as we
 * allow for more and more input types.
 */
const validatePart = function (part, features) {
  if (part.hasOwnProperty('type')) {
    let validTypeFlag = false
    switch (part.type) {
      case 'text':
        validTypeFlag = true
        break
      case 'file':
        validTypeFlag = features.indexOf('rich') !== -1
        break
      case 'location':
        validTypeFlag = features.indexOf('rich') !== -1
        break
      case 'json':
        validTypeFlag = features.indexOf('rich') !== -1
    }
    return validTypeFlag || new Error(`Part of type "${part.type}" is not supported`)
  } else {
    return new Error('A message part must have a type. Options are: [text, file, location, json]')
  }
}

export default {
  Conversation: compose(conversationBase, conversationHistory, conversationIsTyping),
  Message: compose(messageBase, withParts, multimediaLinks)
}
