// Redux-Saga
import { put } from 'redux-saga/effects'
// Events
import eventsMap from '../interface/events'
import { mapEvents } from '../../events/interface/actions'

// The interface to follow.
import { api, reducer, name } from '../interface'

// State setters.
import { update as updateConfig } from '../../config/interface/actions'

// Utilities.
import { mergeValues } from '../../common/utils'
import { logManager } from '../../logs'

// Parse and/or Validate
import { enums, validation as v8n, parse } from '../../../src/common/validation'

// Get the logger
const log = logManager.getLogger('AUTH')

/**
 * Configuration options for the Authentication feature.
 * @public
 * @name config.authentication
 * @memberof config
 * @instance
 * @param {Object} authentication Authentication configs.
 * @param {Object} authentication.server Information for how to reach the platform.
 * @param {string} authentication.server.base Server to be used for requests.
 * @param {string} [authentication.server.protocol=https] Protocol to be used for requests.
 * @param {Number} [authentication.server.port=443] Port to be used for requests.
 * @param {string} authentication.clientCorrelator Unique ID for the client. This is required by the platform to identify an instance of the application used by the specific device.
 */
const defaultOptions = {
  server: {
    base: undefined,
    protocol: 'https',
    port: '443',
    version: 'v1' // not documented, but important
  },
  clientCorrelator: undefined
}

/**
 * CPaaS authentication implementation factory.
 * @method authCpaas
 * @param {Object} options - Configuration options for authentication. See above.
 * @return {Object} plugin - An authentication plugin.
 */

// config validation
const v8nValidation = v8n.schema({
  server: v8n.schema({
    base: v8n.string(),
    protocol: enums(['http', 'https']),
    port: v8n.positive(),
    version: enums(['v1']) // not documented, but important
  }),
  clientCorrelator: v8n.string()
})
const parseOptions = parse('authentication', v8nValidation)

export default function authCpaas (options = {}) {
  options = mergeValues(defaultOptions, options)
  parseOptions(options)

  if (options.websocket) {
    log.warn('Authentication configuration is being replaced, see docs. Please update to the config.')
  }

  if (!options.server.base || !options.clientCorrelator) {
    log.error('Missing required configurations. Please provide proper authentication configurations.')
  }

  function * init () {
    // Send the provided options to the store.
    // This will be `state.config[name]`.
    yield put(updateConfig(options, name))
    yield put(mapEvents(eventsMap))
  }

  const capabilities = ['cpaasAuth']

  return {
    capabilities,
    init,
    api,
    reducer,
    name
  }
}
