/**
 * Authentication state has changed. You can get the new state by calling `getConnection()`.
 *
 * @public
 * @memberof api
 * @requires connect
 * @event auth:change
 * @param {Object} params
 * @param {string} params.reason The cause of the authentication change, provided in the event of an unsolicited disconnection. See the `disconnectReasons` API for possible values.
 */
export const AUTH_CHANGE = 'auth:change'

/**
 * There was an error with authentication.
 *
 * @public
 * @memberof api
 * @requires connect
 * @event auth:error
 * @param {Object} params
 * @param {api.BasicError} params.error The Basic error object.
 *
 */
export const AUTH_ERROR = 'auth:error'

/**
 * An attempt to extend the current user's subscription was made.
 *
 * In a failure scenario, the current user is still connected, and further
 * resubscription attempts will be made, but may become disconnected if the
 * session expires.
 * @public
 * @memberof api
 * @requires connect
 * @event auth:resub
 * @param {Object} params
 * @param {number} params.attemptNum The attempt number of this resubscription.
 * @param {boolean} params.isFailure Whether the resubscription failed or not.
 * @param {api.BasicError} [params.error] The Basic error object.
 */
export const AUTH_RESUB = 'auth:resub'

/**
 * The information (credentials) needed for connecting have been set.
 *
 * @public
 * @memberof api
 * @requires connect
 * @event auth:credentialsSet
 */
export const AUTH_CREDENTIALS_SET = 'auth:credentialsSet'
