/**
 * Presence saga index.
 * Defines which actions trigger which sagas.
 */

// Presence plugin.
import * as subscriptionSagas from './subscriptions'
import * as presenceSagas from './presence'
import * as actionTypes from '../../interface/actionTypes'

// Other plugins.
import { NOTIFICATION_RECEIVED } from '../../../notifications/interface/actionTypes'

// Libraries.
import { takeEvery } from 'redux-saga/effects'
import { registerService } from '../../../subscription/interface/effects'

/**
 * Register the presence service for subscriptions.
 * @method registerPresence
 */
export function * registerPresence () {
  yield registerService('presence', subscriptionSagas.presenceSubscribe, subscriptionSagas.presenceUnsubscribe)
}

/**
 * Presence operations.
 */

/**
 * Subscribe user presence.
 * @method subscribePresence
 */
export function * subscribePresence () {
  yield takeEvery(actionTypes.SUBSCRIBE, presenceSagas.subscribePresence)
}

/**
 * Unsubscribe user presence.
 * @method unsubscribePresence
 */
export function * unsubscribePresence () {
  yield takeEvery(actionTypes.UNSUBSCRIBE, presenceSagas.unsubscribePresence)
}

/**
 * Update user presence.
 * @method updatePresence
 */
export function * updatePresence () {
  yield takeEvery(actionTypes.UPDATE, presenceSagas.updatePresence)
}

/**
 * Fetch user(s) presence.
 * @method fetchPresence
 */
export function * fetchPresence () {
  yield takeEvery(actionTypes.GET, presenceSagas.fetchPresence)
}

export function * receivePresence () {
  yield takeEvery(
    action => action.type === NOTIFICATION_RECEIVED && action.payload.presenceNotification,
    presenceSagas.handleIncomingPresence
  )
}
