import commonIndex from '../core'

// Plugins
import basePlugins from '../core/basePlugins'
import auth from '../../packages/kandy/src/auth/cpaas'
import webrtc from '../../packages/kandy/src/webrtc'
import call from '../../packages/kandy/src/call/cpaas'
import connectivity from '../../packages/kandy/src/connectivity/cpaas'
import messaging from '../../packages/kandy/src/messaging/cpaas'
import notifications from '../../packages/kandy/src/notifications/cpaas'
import presence from '../../packages/kandy/src/presence/cpaas'
import groups from '../../packages/kandy/src/groups/cpaas'
import subscription from '../../packages/kandy/src/subscription/cpaas'
import users from '../../packages/kandy/src/users/cpaas'
import { cpaasRequest } from '../../packages/kandy/src/request'
import '../../packages/kandy/src/docs/docs'

import { createCodecRemover } from '@kandy-io/sdp-handlers'

const defaultPlugins = [
  ...basePlugins,
  { name: 'authentication', fn: auth },
  { name: 'webrtc', fn: webrtc },
  { name: 'call', fn: call },
  { name: 'connectivity', fn: connectivity },
  { name: 'messaging', fn: messaging },
  { name: 'notifications', fn: notifications },
  { name: 'presence', fn: presence },
  { name: 'groups', fn: groups },
  { name: 'subscription', fn: subscription },
  { name: 'users', fn: users },
  { name: 'request', fn: cpaasRequest }
]

function root (options = {}, plugins = []) {
  return commonIndex(options, [...defaultPlugins, ...plugins])
}

// Alias 'create' to be equal to the root function
root.create = root

root.sdpHandlers = {
  createCodecRemover
}

// Export this way as a work-around, so it can be used as `<export>();`.
// See: https://github.com/webpack/webpack/issues/706
module.exports = root
