// Messaging plugin.
import {
  chatSubscribe,
  chatUnsubscribe,
  smsOutboundSubscribe,
  smsOutboundUnsubscribe,
  smsInboundSubscribe,
  smsInboundUnsubscribe
} from '../requests'
import { getMessagingConfig } from '../interface/selectors'

// Other plugins.
import { getRequestInfo } from '../../../auth/interface/selectors'
import { getNotificationChannels } from '../../../subscription/interface/selectors'
import { reportSubscriptionFinished, reportUnsubscriptionFinished } from '../../../subscription/interface/actions'
import { logManager } from '../../../logs'

import BasicError, { subscriptionCodes } from '../../../errors'
import { platforms } from '../../../constants'
// Libraries.
import { call, put, select } from 'redux-saga/effects'

const log = logManager.getLogger('MESSAGING')

/**
 * Plugin chat subscription saga.
 * Handles subscribing to the chat service needed for the messaging plugin.
 * @method chatSubscription
 * @param {Object} config   The subscription request config
 * @param {string} type     The type of notification channel for this subscription.
 */
export function * chatSubscription (config, type) {
  const requestInfo = yield select(getRequestInfo, platforms.CPAAS)
  const channels = yield select(getNotificationChannels)
  const channel = channels.notificationChannels[type]

  log.info(`Subscribing to chat service on ${type} channel.`)

  const response = yield call(chatSubscribe, requestInfo, channel)

  if (response.error) {
    yield put(
      // TODO: Create a subscription effect for this?
      reportSubscriptionFinished({
        service: 'chat',
        type: type,
        error: response.error
      })
    )
  } else {
    yield put(
      reportSubscriptionFinished({
        service: 'chat',
        type: type,
        subscription: response.subscription
      })
    )
  }
}

/**
 * Plugin chat unsubscription saga.
 * Handles unsubscribing from chat service needed for the messaging plugin.
 * @method chatUnsubscription
 */
export function * chatUnsubscription (action) {
  const { subscriptions } = yield select(getNotificationChannels)
  let chatSubscription = subscriptions.filter(sub => {
    return sub.service === 'chat' && sub.channelType === action.payload.type
  })

  if (chatSubscription.length === 0) {
    // This scenario should not occur because we should not be able to subscribe
    //    to an already subscribed-to service.
    log.debug(`No chat subscription on ${action.payload.type} channel found to unsubscribe.`)
    yield put(
      reportUnsubscriptionFinished({
        error: new BasicError({
          message: 'Multiple chat subscriptions found. Cannot unsubscribe.',
          code: subscriptionCodes.CPAAS_UNSUBSCRIBE_FAIL
        }),
        service: 'chat',
        type: action.payload.type
      })
    )
    return
  } else if (chatSubscription.length > 1) {
    // "Error" scenario, why are there more than 1 chat subscriptions?
    // TODO: Report ...something.
    yield put(reportUnsubscriptionFinished())
    return
  } else {
    chatSubscription = chatSubscription[0]
  }

  log.info(`Unsubscribing from chat service on ${action.payload.type} channel.`)
  const requestInfo = yield select(getRequestInfo, platforms.CPAAS)

  const response = yield call(chatUnsubscribe, requestInfo, chatSubscription)

  yield put(
    reportUnsubscriptionFinished({
      ...response,
      service: 'chat',
      type: action.payload.type
    })
  )
}

/**
 * Subscription saga for inbound SMS notifications.
 * @method smsInboundSubscription
 * @param {Object} config   The subscription request config
 * @param {string} type     The type of notification channel for this subscription.
 */
export function * smsInboundSubscription (config, type) {
  const requestInfo = yield select(getRequestInfo, platforms.CPAAS)
  const channels = yield select(getNotificationChannels)
  const channel = channels.notificationChannels[type]

  if (config && config.params && config.params.destinationAddress) {
    requestInfo.destinationAddress = config.params.destinationAddress
  } else {
    // we need to be backwards compatible, so get the sms number from global configuration obj.
    const messagingConfig = yield select(getMessagingConfig)
    requestInfo.destinationAddress = messagingConfig.smsFrom
  }
  log.debug(
    `Subscribing to SMS inbound service on ${type} channel for destination address: ${requestInfo.destinationAddress}.`
  )

  const response = yield call(smsInboundSubscribe, requestInfo, channel)

  yield put(
    reportSubscriptionFinished({
      ...response,
      service: 'smsinbound',
      type: type
    })
  )
}

/**
 * Unsubscription saga for inbound SMS notifications.
 * @method smsInboundUnsubscription
 * @param  {Object} action
 */
export function * smsInboundUnsubscription (action) {
  const { subscriptions } = yield select(getNotificationChannels)
  let smsSubscription = subscriptions.filter(sub => {
    return sub.service === 'smsinbound' && sub.channelType === action.payload.type
  })

  if (smsSubscription.length === 1) {
    smsSubscription = smsSubscription[0]

    log.debug(`Unsubscribing from inbound SMS service on ${action.payload.type} channel.`)
    const requestInfo = yield select(getRequestInfo, platforms.CPAAS)
    const response = yield call(smsInboundUnsubscribe, requestInfo, smsSubscription)

    yield put(
      reportUnsubscriptionFinished({
        ...response,
        service: 'smsinbound',
        type: action.payload.type
      })
    )
  } else {
    // "Error" scenario: Should only be one subscription like this.
  }
}

