import * as actionTypes from '../actions/actionTypes'
import { unset } from 'lodash/fp'

const reducers = {}

const contactsPendingReducer = {
  next (state) {
    return {
      ...state,
      isPending: true
    }
  }
}

reducers[actionTypes.ADD_CONTACT] = contactsPendingReducer
reducers[actionTypes.REFRESH_CONTACTS] = contactsPendingReducer
reducers[actionTypes.FETCH_CONTACT] = contactsPendingReducer
reducers[actionTypes.UPDATE_CONTACT] = contactsPendingReducer

reducers[actionTypes.ADD_CONTACT_FINISH] = {
  next (state, action) {
    return {
      ...state,
      contacts: {
        ...state.contacts,
        [action.payload.contactId]: action.payload
      },
      isPending: false
    }
  },
  throw (state, action) {
    return {
      ...state,
      isPending: false,
      errors: state.errors.concat(action.payload)
    }
  }
}

reducers[actionTypes.REFRESH_CONTACTS_FINISH] = {
  next (state, action) {
    const contacts = {}
    for (const contact of action.payload) {
      contacts[contact.contactId] = contact
    }
    return {
      ...state,
      contacts: contacts,
      isPending: false
    }
  },
  throw (state, action) {
    return {
      ...state,
      isPending: false,
      errors: state.errors.concat(action.payload)
    }
  }
}

reducers[actionTypes.FETCH_CONTACT_FINISH] = {
  next (state, action) {
    return {
      ...state,
      contacts: {
        ...state.contacts,
        [action.payload.contactId]: action.payload
      },
      isPending: false
    }
  },
  throw (state, action) {
    return {
      ...state,
      isPending: false,
      errors: state.errors.concat(action.payload)
    }
  }
}

reducers[actionTypes.REMOVE_CONTACT_FINISH] = {
  next (state, action) {
    return {
      ...state,
      contacts: unset(action.payload, state.contacts), // Remove the property from state.contacts whose key is equal to action.payload
      isPending: false
    }
  },
  throw (state, action) {
    return {
      ...state,
      isPending: false,
      errors: state.errors.concat(action.payload)
    }
  }
}

reducers[actionTypes.UPDATE_CONTACT_FINISH] = {
  next (state, action) {
    return {
      ...state,
      contacts: {
        ...state.contacts,
        [action.payload.contactId]: action.payload
      },
      isPending: false
    }
  },
  throw (state, action) {
    return {
      ...state,
      isPending: false,
      errors: state.errors.concat(action.payload)
    }
  }
}

export default reducers
