// Presence plugin.

// Helpers.
import request from '../../../request/effects'
import { handleRequestError } from '../../../common/helpers'

/**
 * REST request to create a presence list subscription.
 * @method subscribe
 * @param  {string} presenceListId Resource ID of the presenceList
 * @param  {string} callbackURL Callback URL of websocket channel
 * @param  {Object} requestInfo
 * @return {Object}
 */
export function * subscribe (presenceListId, callbackURL, requestInfo) {
  const presenceListSubscription = {
    presenceListSubscription: {
      callbackReference: {
        notifyURL: callbackURL
      },
      clientCorrelator: requestInfo.clientCorrelator,
      duration: 86400
    }
  }

  const requestOptions = {
    method: 'POST',
    url:
      `${requestInfo.baseURL}/cpaas/presence/${requestInfo.version}/` +
      `${requestInfo.username}/subscriptions/presenceListSubscriptions/` +
      `${presenceListId}`,
    body: JSON.stringify(presenceListSubscription)
  }

  const response = yield request(requestOptions)

  if (response.error) {
    return {
      error: handleRequestError(response, 'Subscribe to presence list')
    }
  } else {
    return response.payload.body
  }
}

/**
 * REST request to delete a subscription against a presence list.
 * @method unsubscribe
 * @param  {string} presenceListId Resource ID of the presenceList
 * @param  {string} subscriptionId Resource ID of the subscription
 * @param  {Object} requestInfo
 * @return {Object}
 */
export function * unsubscribe (presenceListId, subscriptionId, requestInfo) {
  const requestOptions = {
    method: 'DELETE',
    responseType: 'none',
    url:
      `${requestInfo.baseURL}/cpaas/presence/${requestInfo.version}/` +
      `${requestInfo.username}/subscriptions/presenceListSubscriptions/` +
      `${presenceListId}/${subscriptionId}`
  }

  const response = yield request(requestOptions)

  if (response.error && response.payload.result.code !== 404) {
    return {
      error: handleRequestError(response, 'Unsubscribe from presence list')
    }
  } else {
    return response.payload.body
  }
}
