/**
 * Users saga index.
 * Defines which actions trigger which sagas.
 */

// Users plugin.
import * as contactsSagas from './contacts'
import * as usersSagas from './users'
import * as actionTypes from '../../interface/actions/actionTypes'

// Libraries.
import { takeEvery } from 'redux-saga/effects'

// Contacts sagas

/**
 * addContact
 *
 * Add a contact to the personal address book of the subscribed user.
 * @method addContact
 */
export function * addContact () {
  yield takeEvery(actionTypes.ADD_CONTACT, contactsSagas.addContact)
}

/**
 * updateContact
 *
 * Update a contact to the personal address book of the subscribed user.
 * @method updateContact
 */
export function * updateContact () {
  yield takeEvery(actionTypes.UPDATE_CONTACT, contactsSagas.updateContact)
}

/**
 * removeContact
 *
 * Remove a contact from the currently subscribed user's address book.
 * @method removeContact
 */
export function * removeContact () {
  yield takeEvery(actionTypes.REMOVE_CONTACT, contactsSagas.removeContact)
}

/**
 * fetchContact
 *
 * Fetch a contact from the personal address book of the subscribed user.
 * @method fetchContact
 */
export function * fetchContact () {
  yield takeEvery(actionTypes.FETCH_CONTACT, contactsSagas.fetchContact)
}

/**
 * refreshContacts
 *
 * Refresh the contacts of the currently subscribed user.
 * @method refreshContacts
 */
export function * refreshContacts () {
  yield takeEvery(actionTypes.REFRESH_CONTACTS, contactsSagas.refreshContacts)
}

// Users sagas

/**
 * searchDirectory
 *
 * Search the user directory.
 * @method searchDirectory
 */
export function * searchDirectory () {
  yield takeEvery(actionTypes.SEARCH_DIRECTORY, usersSagas.searchDirectory)
}

/**
 * fetchUser
 *
 * Fetch a user from the directory service.
 * @method fetchUser
 */
export function * fetchUser () {
  yield takeEvery(actionTypes.FETCH_USER, usersSagas.fetchUser)
}

/**
 * fetchSelfInfo
 *
 * Fetch user information for the currently logged in user.
 * @method fetchSelfInfo
 */
export function * fetchSelfInfo () {
  yield takeEvery(actionTypes.FETCH_SELF_INFO, usersSagas.fetchSelfInfo)
}
