export function * rejectInvitationRequest (groupId, requestInfo, userInfo) {
  const url =
    `${requestInfo.baseURL}/cpaas/chat/${requestInfo.version}/` +
    `${requestInfo.username}/group/${groupId}/participants/${userInfo.identity}`
  const requestOptions = {
    method: 'DELETE',
    responseType: 'none',
    url
  }
  const response = yield request(requestOptions)

  if (response.error) {
    return {
      error: handleRequestError(response, 'Reject Invitation')
    }
  } else {
    return {
      error: false
    }
  }
}
