import * as actionTypes from './actionTypes'

// Libraries
import { handleActions } from 'redux-actions'

const reducers = {}

reducers[actionTypes.CREATE_GROUP_FINISH] = {
  next (state, action) {
    // rename groupId to id
    const newGroup = { ...action.payload.group, id: action.payload.group.groupId }
    delete newGroup.groupId

    return { ...state, groups: [...state.groups, newGroup] }
  }
}

/*
 * Removes the specified group from state.
 */
reducers[actionTypes.DELETE_GROUP_FINISH] = {
  next (state, action) {
    const { groupId } = action.payload

    return {
      ...state,
      groups: state.groups.filter(group => group.id !== groupId)
    }
  }
}

/*
 * Adds a participant to the specified group.
 */
reducers[actionTypes.ADD_PARTICIPANT_FINISH] = {
  next (state, action) {
    const { groupId, participant } = action.payload

    return {
      ...state,
      groups: state.groups.map(group => {
        if (group.id === groupId) {
          return {
            ...group,
            participants: [...group.participants, participant]
          }
        }
        return group
      })
    }
  }
}

/*
 * Removes a participant from the specified group.
 */
reducers[actionTypes.REMOVE_PARTICIPANT_FINISH] = {
  next (state, action) {
    const { groupId, participant } = action.payload
    return {
      ...state,
      groups: state.groups.map(group => {
        if (group.id === groupId) {
          return {
            ...group,
            participants: group.participants.filter(member => member.address !== participant)
          }
        }
        return group
      })
    }
  }
}

/*
 * Leave a specified group and remove invitation in state if it exists.
 */
reducers[actionTypes.LEAVE_GROUP_FINISH] = {
  next (state, action) {
    const { groupId } = action.payload

    return {
      ...state,
      groups: state.groups.filter(group => group.id !== groupId),
      invitations: state.invitations.filter(invite => invite.id !== groupId)
    }
  }
}

/*
 * Fetch list of groups from server and update state.
 */
reducers[actionTypes.FETCH_GROUPS_FINISH] = {
  next (state, action) {
    // rename groupId to id
    const groups = action.payload.groups.map(group => {
      const groupWithId = { ...group, id: group.groupId }
      delete groupWithId.groupId
      return groupWithId
    })

    return {
      ...state,
      groups
    }
  }
}

/*
 * Accept the invitation and update the state.
 */
reducers[actionTypes.ACCEPT_INVITATION_FINISH] = {
  next (state, action) {
    const { group } = action.payload
    const newGroup = { ...group, id: group.groupId }
    delete newGroup.groupId

    // remove the invitation and replace the group in state.
    return {
      ...state,
      groups: [...state.groups.filter(group => group.id !== newGroup.id), newGroup],
      invitations: state.invitations.filter(invite => invite.id !== newGroup.id)
    }
  }
}

/*
 * Reject the invitation and update the state.
 */
reducers[actionTypes.REJECT_INVITATION_FINISH] = {
  next (state, action) {
    const { groupId } = action.payload

    // remove the invitation and the group from state.
    return {
      ...state,
      groups: state.groups.filter(group => group.id !== groupId),
      invitations: state.invitations.filter(invite => invite.id !== groupId)
    }
  }
}

/*
 * Invitation has been received.
 */
reducers[actionTypes.INVITATION_RECEIVED] = {
  next (state, action) {
    const invitation = { ...action.payload.invitation, id: action.payload.invitation.groupId }
    delete invitation.groupId

    return {
      ...state,
      invitations: [...state.invitations, invitation]
    }
  }
}

/*
 * A group that you belong to has been deleted.
 */
reducers[actionTypes.EVENT_NOTIFICATION_RECEIVED] = {
  next (state, action) {
    const { groupId, notification } = action.payload

    // if the eventType is 'SessionInvited' (ie you have an unaccepted invitation),
    // remove the group and invitation from state.
    if (notification.eventType === 'SessionCancelled') {
      return {
        ...state,
        groups: state.groups.filter(group => group.id !== groupId),
        invitations: state.invitations.filter(invite => invite.id !== groupId)
      }
      // otherwise, just remove the group
    } else if (notification.eventType === 'SessionEnded') {
      return {
        ...state,
        groups: state.groups.filter(group => group.id !== groupId)
      }
    }
  }
}
/*
 * A group that you belong to has been modified.
 */
reducers[actionTypes.STATUS_NOTIFICATION_RECEIVED] = {
  next (state, action) {
    // need to update the state.... if the group exists in state, update this participants
    const { groupId, notification } = action.payload
    const changedParticipant = notification.participants[0]
    const newState = {
      ...state,
      groups: state.groups.map(group => {
        if (group.id === groupId) {
          const newGroup = {
            ...group,
            participants: group.participants.filter(participant => participant.address !== changedParticipant.address)
          }
          newGroup.participants.push(changedParticipant)
          return {
            ...newGroup
          }
        }
        return group
      })
    }
    return newState
  }
}

const reducer = handleActions(reducers, { groups: [], invitations: [] })
export default reducer
