import * as actionTypes from '../actions/actionTypes'

const reducers = {}

reducers[actionTypes.FETCH_USER_FINISH] = {
  next (state, action) {
    const user = action.payload
    return {
      ...state,
      users: {
        ...state.users,
        [user.userId]: user
      }
    }
  },
  throw (state, action) {
    return {
      ...state,
      isPending: false,
      errors: state.errors.concat(action.payload)
    }
  }
}

reducers[actionTypes.FETCH_SELF_INFO_FINISH] = {
  next (state, action) {
    const self = action.payload
    return {
      ...state,
      users: {
        ...state.users,
        [self.userId]: self
      }
    }
  },
  throw (state, action) {
    return {
      ...state,
      isPending: false,
      errors: state.errors.concat(action.payload)
    }
  }
}

reducers[actionTypes.SEARCH_DIRECTORY_FINISH] = {
  next (state, action) {
    const users = {}
    for (const user of action.payload) {
      users[user.userId] = user
    }
    return {
      ...state,
      users: {
        ...state.users,
        ...users
      },
      isPending: false
    }
  },
  throw (state, action) {
    return {
      ...state,
      isPending: false,
      errors: state.errors.concat(action.payload)
    }
  }
}

export default reducers
