// Subscription plugin.
import { requestWebsocket, deleteChannel } from '../requests'
import * as actions from '../../interface/actions'
import { getSubscriptionConfig, getNotificationChannels } from '../../interface/selectors'

// Other plugins.
import { getAuthConfig, getRequestInfo } from '../../../auth/interface/selectors'
import { connectWebsocket, disconnectWebsocket } from '../../../connectivity/interface/effects'
import { logManager } from '../../../logs'

import { getConnectivityConfig, getConnectionState } from '../../../connectivity/interface/selectors'
// Helpers.
import BasicError, { subscriptionCodes } from '../../../errors'
import { notificationTypes, platforms } from '../../../constants'

// Libraries.
import { call, put, select } from 'redux-saga/effects'

const platform = platforms.CPAAS
const log = logManager.getLogger('SUBSCRIPTION')

/**
 * Ensures that a notification channel of `type` is open.
 * If already open, does nothing. If not open, requests/opens it.
 * @method ensureChannel
 * @param {string} type The type of notification channel.
 * @return {Object} The result containing a boolean property: success.
 *                  If success property is false, an extra error property will also be present.
 */
export function * ensureChannelOpen (type) {
  /**
   * Assumed format of the subInfo object:
   * @param {Object} subInfo
   * @param {Object} subInfo.notificationChannels A mapping of types to the channel info.
   * @param {Array} subInfo.subscriptions A list of active subscriptions.
   */
  const subInfo = yield select(getNotificationChannels)

  // If there is no open notification channel for `type`, request it.
  if (!subInfo.notificationChannels[type]) {
    let channelResponse
    if (type === notificationTypes.WEBSOCKET) {
      log.info(`Opening websocket channel for platform ${platform}.`)
      channelResponse = yield call(openWebsocketChannel, platform)
    } else if (type === notificationTypes.PUSH) {
      // TODO: CPaaS Push subscription here.
    } else {
      // Bad scenario.
    }

    if (channelResponse.error) {
      log.info('Failed to open websocket channel. Error: ', channelResponse.error)
      // Error scenario: Could not open websocket channel.
      return { success: false, error: channelResponse.error }
    }

    // Save newly opened channel info in state.
    yield put(
      actions.channelOpened(
        {
          ...channelResponse.notificationChannel,
          channelId: channelResponse.notificationChannel.resourceURL.split('/channels/')[1]
        },
        type
      )
    )
    log.info('Successfully opened websocket channel.')
    return { success: true }
  } else {
    // If there is already a channel, use it.
    return { success: true }
  }
}

/**
 * This wsSubscribe function makes a request to the CPAAS gateway for a subscription.
 * If successful it triggers a subscribeFinished action containing subscription information.
 * @method openWebsocketChannel
 */
export function * openWebsocketChannel (platform) {
  const requestInfo = yield select(getRequestInfo, platforms.CPAAS)
  const subscriptionInfo = yield select(getSubscriptionConfig)
  const connectivityInfo = yield select(getConnectivityConfig)

  // Request a websocket to subscribe to.
  const response = yield call(requestWebsocket, requestInfo, subscriptionInfo, connectivityInfo)

  if (response.error) {
    return response
  }

  const subscription = {
    ...response.notificationChannel,
    service: 'CPaaS',
    url: response.notificationChannel.channelData.channelURL
  }

  const connectionInfo = yield select(getAuthConfig)
  // Format the response to pass off to the connectivity plugin.
  const websocketInfo = {
    protocol: 'wss',
    server: connectionInfo.server.base,
    port: connectionInfo.server.port,
    url: subscription.url,
    params: {
      access_token: requestInfo.token
    }
  }

  // Request the websocket connection.
  const wsOpenOrError = yield connectWebsocket(websocketInfo, platform)

  if (wsOpenOrError.error) {
    log.info('Failed to connect to websocket. Error: ', wsOpenOrError.error)
    // TODO: the connectivity plugin should be creating (and passing on) the websocket errors.
    throw new BasicError({
      message: 'Failed to connect to the websocket.',
      code: subscriptionCodes.WS_CONNECTION_ERROR
    })
  } else {
    // We're connected. Finish subscription process.
    // TODO: Once auth is complete, fill the data.
    return {
      // We will only ever have one of each notificationChannel type. So this should be the only websocket channel in state.
      notificationChannel: response.notificationChannel,
      type: notificationTypes.WEBSOCKET
    }
  }
}
/**
 * Close the specified channel by cleaning up its resources then deleting it.
 * @method closeChannel
 * @param  {Object} channel Information about the channel to be closed.
 * @param  {string} platform
 * @return {Object}
 */
export function * closeChannel (channel, platform) {
  const { channelId, channelType } = channel

  // TODO: Don't hardcode a constant.
  if (channelType === 'websockets') {
    // Ask the connectivity plugin to disconnect this platform's websocket if it exists
    const wsState = yield select(getConnectionState, platform)
    if (wsState.connected) {
      log.info(`Closing websocket channel for platform ${platform}.`)
      const closeAction = yield disconnectWebsocket(undefined, platform)

      if (closeAction.error) {
        log.info(`Failed to close websocket. Error: ${closeAction.error} Continuing anyway.`)
      }
    }

    const requestInfo = yield select(getRequestInfo, platforms.CPAAS)

    // Delete the notification channel resource.
    log.info(`Deleting channel with id: ${channelId}`)
    const response = yield call(deleteChannel, channelId, requestInfo)
    return response
  } else {
    // TODO: Handle closing other channel types.
  }
}
