// Presence plugin.
import { getPresenceLists, createList } from '../requests/presence'
import { subscribe, unsubscribe } from '../requests/subscriptions'

// Other plugins.
import { reportSubscriptionFinished, reportUnsubscriptionFinished } from '../../../subscription/interface/actions'
import { logManager } from '../../../logs'
import { getRequestInfo } from '../../../auth/interface/selectors'
import { getNotificationChannels } from '../../../subscription/interface/selectors'
import BasicError from '../../../errors'

// Helpers.
import { platforms } from '../../../constants'

// Libraries.
import { call, put, select } from 'redux-saga/effects'

const log = logManager.getLogger('PRESENCE')

/**
 * Subscription saga. Creates a new presence subscription channel.
 * @method presenceSubscribe
 * @param {Object} config   The subscription request config
 * @param {string} type     The type of notification channel for this subscription.
 */
export function * presenceSubscribe (config, type) {
  const requestInfo = yield select(getRequestInfo, platforms.CPAAS)
  const channels = yield select(getNotificationChannels)

  const callbackURL = channels.notificationChannels.websocket.callbackURL

  log.debug('Subscribing to presence service')
  // either use an existing presence list, or create a new one
  const presenceListResponse = yield call(getPresenceLists, requestInfo)
  log.debug('Received presenceLists response:', presenceListResponse)

  // check if there is a presence list already
  let presenceListId, newListResponse
  if (presenceListResponse instanceof BasicError) {
    yield put(
      reportSubscriptionFinished({
        service: 'presence',
        type: 'websocket',
        error: presenceListResponse
      })
    )
    return
  } else {
    // check if there is an existing presenceList
    if (presenceListResponse.presenceList.length > 0) {
      const url = presenceListResponse.presenceList[0].resourceURL
      if (url) {
        const lastIndex = url.lastIndexOf('/')
        presenceListId = url.slice(lastIndex + 1)
      }
    } else {
      //  create a new presenceList
      newListResponse = yield call(createList, [], requestInfo)

      if (newListResponse instanceof BasicError) {
        yield put(
          reportSubscriptionFinished({
            service: 'presence',
            type: 'websocket',
            error: newListResponse
          })
        )
        return
      }

      const resourceURL = newListResponse.resourceURL
      if (resourceURL) {
        presenceListId = resourceURL.substring(resourceURL.lastIndexOf('/') + 1)
      }
    }
  }

  // Subscribe to the presence list subscription
  const response = yield call(subscribe, presenceListId, callbackURL, requestInfo)
  log.debug('Received subscribe presence response:', response)

  if (response.error) {
    yield put(
      reportSubscriptionFinished({
        service: 'presence',
        type: 'websocket',
        error: response.error
      })
    )
  } else {
    yield put(
      reportSubscriptionFinished({
        service: 'presence',
        type: 'websocket',
        subscription: response.presenceListSubscription
      })
    )
  }
}

/**
 * Unsubscription saga. Deletes an existing presence subscription.
 * @method presenceUnsubscribe
 */
export function * presenceUnsubscribe (action) {
  const { subscriptions } = yield select(getNotificationChannels)
  let presenceSubscription = subscriptions.filter(sub => {
    return sub.service === 'presence' && sub.channelType === action.payload.type
  })

  if (presenceSubscription.length === 0) {
    // "Error" scenario: Can't unsubscribe to a service
    //    that we don't have.
  } else if (presenceSubscription.length > 1) {
    // "Error" scenario: There shouldn't be able to have
    //    more than 1 of the same service.
  } else {
    presenceSubscription = presenceSubscription[0]
  }
  const presenceListId = presenceSubscription.presenceListId
  const subscription = presenceSubscription.subscriptionId
  const subscriptionId = subscription.substring(subscription.lastIndexOf('/') + 1)

  log.info(`Unsubscribing from presence service on ${action.payload.type} channel.`)
  const requestInfo = yield select(getRequestInfo, platforms.CPAAS)

  // Unsubscribe from the presence list subscription
  const response = yield call(unsubscribe, presenceListId, subscriptionId, requestInfo)

  yield put(
    reportUnsubscriptionFinished({
      ...response,
      service: 'presence',
      type: action.payload.type
    })
  )
}
