// Messaging plugin.
import { messageActions, convoActions } from '../interface/actions'
import {
  sendSMSRequest,
  sendChatMessageRequest,
  sendGroupChatMessageRequest,
  fetchConversationsRequest,
  fetchSmsConversationsRequest,
  deleteChatConversationRequest,
  deleteChatMessageRequest,
  fetchMessagesRequest,
  fetchSmsMessagesRequest,
  uploadFile,
  setIsTypingRequest,
  fetchImageLinks
} from '../requests'

// Other plugins.
import { findConversation, getMessagingConfig } from '../interface/selectors'
import { getRequestInfo } from '../../../auth/interface/selectors'

import { platforms } from '../../../constants'
import { DeliveryStatuses, chatTypes } from '../../mappings'

// Libraries.
import { call, put, select, all } from 'redux-saga/effects'

// Logging
import { logManager } from '../../../logs'
import BasicError, { messagingCodes } from '../../../errors'
const log = logManager.getLogger('MESSAGING')

/**
 * Handles isTypingNotifications and sets isTypingList in redux state
 * @method receiveIsTypingNotification
 * @param {Object} action A 'NOTIFICATION_RECEIVED' action.
 */
export function * receiveIsTypingNotification (action) {
  const {
    state,
    senderAddress,
    'x-destinationAddress': destinationAddress
  } = action.payload.chatMessageNotification.isComposing

  // use helper function to get sessionType from notifications
  const sessionType = getNotificationType(action.payload.chatMessageNotification, 'chatSession')
  if (sessionType !== chatTypes.GROUP && sessionType !== chatTypes.ONETOONE) {
    yield put(
      convoActions.setIsTypingFinished({
        error: new BasicError({
          message: `Invalid chat type. Type must be ${chatTypes.GROUP} or ${chatTypes.ONETOONE}.`,
          code: messagingCodes.SET_IS_TYPING_FAIL
        })
      })
    )
  }
  // For group chat, we need to use the destinationAddress (groupId)
  const destination = sessionType === chatTypes.ONETOONE ? senderAddress : destinationAddress

  yield put(
    convoActions.setIsTypingFinished({
      state,
      senderAddress: senderAddress,
      destination: [destination],
      type: sessionType
    })
  )
}

/**
 * Sets the typing status of the current user in redux state
 * @method setIsTyping
 * @param {Object} action A 'SET_IS_TYPING' action.
 */
export function * setIsTyping (action) {
  const requestInfo = yield select(getRequestInfo, platforms.CPAAS)
  const type = action.payload.type
  const response = yield call(setIsTypingRequest, requestInfo, action.payload.destination, action.payload.state, type)
  const { state, senderAddress, 'x-destinationAddress': destination, error } = response

  if (error) {
    yield put(
      convoActions.setIsTypingFinished({
        error: error
      })
    )
  } else {
    yield put(
      convoActions.setIsTypingFinished({
        state,
        senderAddress: senderAddress,
        destination: [destination],
        type: type
      })
    )
  }
}

/**
 * Sends chat messages via POST request
 * @method sendChatMessage
 * @param  {Object} action A `SEND_MESSAGE` action
 */
export function * sendChatMessage (action) {
  const requestInfo = yield select(getRequestInfo, platforms.CPAAS)

  let chatResponse
  const chatType = action.payload.message.type

  let textParts = []
  let fileParts = []
  action.payload.message.parts.forEach(part => {
    if (part.type === 'text') {
      textParts = textParts.concat([part])
    } else if (part.type === 'file') {
      fileParts = fileParts.concat([part])
    } else {
      log.info('Unknown part type: ' + part.type)
    }
  })

  // TODO: when the backend API expands its functionality this log/error should be removed
  if (fileParts.length > 1) {
    log.info("Too many fileParts to upload, API doesn't currently support multiple fileParts.")
    yield put(
      messageActions.sendMessageFinish({
        error: new BasicError({
          message: "Too many fileParts to upload, API doesn't currently support multiple fileParts.",
          code: messagingCodes.INVALID_FILE_PARTS
        })
      })
    )
  }

  let attachmentParts = []
  if (fileParts.length > 0) {
    const uploadResponseList = yield all(fileParts.map(filePart => call(uploadFile, requestInfo, filePart.file)))

    attachmentParts = uploadResponseList.map(response => {
      return { ...response.attachment, type: 'file', rawURL: response.attachment.link }
    })
    // multimedia messaging requires a specific format for each uploaded file
    const chatMediaParts = attachmentParts.map(part => {
      return { name: part.name, 'x-id': part['x-id'] }
    })

    if (chatType === chatTypes.ONETOONE) {
      chatResponse = yield call(
        sendChatMessageRequest,
        requestInfo,
        action.payload.destination[0],
        textParts[0].text,
        chatMediaParts
      )
    } else if (chatType === chatTypes.GROUP) {
      chatResponse = yield call(
        sendGroupChatMessageRequest,
        requestInfo,
        action.payload.destination[0],
        textParts[0].text,
        chatMediaParts
      )
    }
  } else {
    // hardcoding textParts because API only allows sending one text part
    if (chatType === chatTypes.ONETOONE) {
      chatResponse = yield call(sendChatMessageRequest, requestInfo, action.payload.destination[0], textParts[0].text)
    } else if (chatType === chatTypes.GROUP) {
      chatResponse = yield call(
        sendGroupChatMessageRequest,
        requestInfo,
        action.payload.destination[0],
        textParts[0].text
      )
    }
  }
  const allParts = textParts.concat(attachmentParts)
  const finishInfo = {
    type: chatType,
    destination: action.payload.destination,
    sender: chatResponse.senderAddress,
    deliveryStatus: chatResponse.status,
    parts: allParts,
    timestamp: action.payload.message.timestamp
  }

  if (chatResponse.error) {
    yield put(
      messageActions.sendMessageFinish({
        ...finishInfo,
        error: chatResponse.error
      })
    )
  } else {
    const messageId = chatResponse.resourceURL.split('/messages/')[1]
    yield put(
      messageActions.sendMessageFinish({
        ...finishInfo,
        messageId
      })
    )
  }
}

/**
 * Deletes a conversation via DELETE request
 * @method deleteConversation
 * @param  {Object} action A `DELETE_CONVERSATION` action
 */
export function * deleteConversation (action) {
  const requestInfo = yield select(getRequestInfo, platforms.CPAAS)
  const chatType = action.payload.type

  if (chatType === chatTypes.SMS) {
    // sms delete requires originators phone number, which we'll get from the id passed in action's payload
    if (action.payload.id) {
      requestInfo.senderAddress = action.payload.id.split('--')[0]
    } else {
      // Maintaining backwards compatibility
      const messagingConfig = yield select(getMessagingConfig)
      requestInfo.senderAddress = messagingConfig.smsFrom || 'default'
    }
  }
  const deleteResponse = yield call(deleteChatConversationRequest, requestInfo, action.payload.destination[0], chatType)

  if (deleteResponse.error) {
    yield put(
      convoActions.deleteConversationFinish({
        type: chatType,
        destination: action.payload.destination,
        error: deleteResponse.error
      })
    )
  } else {
    yield put(
      convoActions.deleteConversationFinish({
        type: chatType,
        destination: action.payload.destination
      })
    )
  }
}

/**
 * Deletes a message via DELETE request
 * @method deleteMessage
 * @param  {Object} action A `DELETE_MESSAGE` action
 */
export function * deleteMessage (action) {
  const requestInfo = yield select(getRequestInfo, platforms.CPAAS)

  const chatType = action.payload.type
  if (chatType === chatTypes.SMS) {
    // sms delete requires originators phone number, which we'll get from the id passed in action's payload
    if (action.payload.id) {
      requestInfo.senderAddress = action.payload.id.split('--')[0]
    } else {
      // Maintain backwards compatibility
      const messagingConfig = yield select(getMessagingConfig)
      requestInfo.senderAddress = messagingConfig.smsFrom || 'default'
    }
  }

  const deleteResponse = yield call(
    deleteChatMessageRequest,
    requestInfo,
    action.payload.destination[0],
    action.payload.messageId,
    chatType
  )

  if (deleteResponse.error) {
    yield put(
      messageActions.deleteMessageFinish({
        type: chatType,
        destination: action.payload.destination,
        messageId: action.payload.messageId,
        error: deleteResponse.error
      })
    )
  } else {
    yield put(
      messageActions.deleteMessageFinish({
        type: chatType,
        destination: action.payload.destination,
        messageId: action.payload.messageId
      })
    )
  }
}

/**
 * Handles incoming chat message notifications
 * @method handleChatMessageNotification
 * @param  {Object} action A `NOTIFICATION_RECEIVED` action.
 */
export function * handleChatMessageNotification (action) {
  const {
    dateTime,
    senderAddress,
    text,
    attachment,
    'x-destinationAddress': destinationAddress
  } = action.payload.chatMessageNotification.chatMessage

  // use helper function to get message type from notification
  const messageType = getNotificationType(action.payload.chatMessageNotification, 'chatMessage')

  // Grab the messageId from the end of link.href
  const messageId = action.payload.chatMessageNotification.link[0].href.split('/messages/')[1]

  let destination
  if (messageType === chatTypes.GROUP) {
    destination = [destinationAddress]
  } else {
    destination = [senderAddress]
  }

  const existingConversation = yield select(findConversation, destination, messageType)
  const newConversation = !existingConversation

  let parts = []
  if (text) {
    const textPart = [{ type: 'text', text }]
    parts = parts.concat(textPart)
  }

  if (attachment) {
    const fileParts = attachment.map(file => {
      return {
        type: 'file',
        rawURL: file.link,
        name: file.name,
        size: file.size,
        'x-id': file['x-id']
      }
    })
    parts = parts.concat(fileParts)
  }

  yield put(
    messageActions.messageReceived(destination, parts, messageId, senderAddress, dateTime, {
      type: messageType,
      newConversation: newConversation
    })
  )
}

/**
 * Handles delivery receipts for SMS messages via websocket messages
 * @method handleDeliveryReceipts
 * @param  {Object} action A `NOTIFICATION_RECEIVED` action representing a deliveryReceipt.
 */
export function * handleDeliveryReceipts (action) {
  const deliveryInfo = action.payload.deliveryInfoNotification.deliveryInfo
  let deliveryStatus = null
  if (DeliveryStatuses[deliveryInfo.deliveryStatus]) {
    deliveryStatus = DeliveryStatuses[deliveryInfo.deliveryStatus]
  } else {
    log.info(`Invalid deliveryStatus: ${deliveryInfo.deliveryStatus}, defaulting to Unknown`)
    deliveryStatus = DeliveryStatuses.Unknown
  }
  const messageId = deliveryInfo.messageId
  const destination = `${deliveryInfo.address}`
  yield put(messageActions.deliveryReceiptReceived(messageId, deliveryStatus, [destination], chatTypes.SMS))
}

/**
 * Handles "send message" actions specifically for SMS.
 * @method sendSMS
 * @param  {Object} action A `SEND_MESSAGE` action representing a SMS Message.
 */
export function * sendSMS (action) {
  const requestInfo = yield select(getRequestInfo, platforms.CPAAS)

  if (action.payload.id) {
    // id is the current conversation id, which is  of the form: <senderAddress>--<destinationAddress>
    // We just need to extract the senderAddress.
    requestInfo.senderAddress = action.payload.id.split('--')[0]
  } else {
    // Maintain backwards compatibility
    const messagingConfig = yield select(getMessagingConfig)
    requestInfo.senderAddress = messagingConfig.smsFrom || 'default'
  }

  const { destination, message } = action.payload
  const response = yield call(sendSMSRequest, requestInfo, destination[0], message.parts[0].text)

  // TODO: Do something with the rest of the information from the response?
  //      The finish action is specific for pre-CPaaS messages, so its
  //      awkward to pass on/store all the new information.
  const finishInfo = {
    sender: requestInfo.username,
    destination: destination,
    parts: message.parts,
    timestamp: message.timestamp,
    type: message.type
  }

  if (response.error) {
    yield put(
      messageActions.sendMessageFinish({
        ...finishInfo,
        deliveryStatus: DeliveryStatuses.DeliveryImpossible,
        error: response.error
      })
    )
  } else {
    // the ID is in the end of a string (`.../messages/<ID>`), so get it.
    const messageId = response.resourceURL.split('/messages/')[1]
    yield put(
      messageActions.sendMessageFinish({
        ...finishInfo,
        deliveryStatus: DeliveryStatuses[response.deliveryInfoList.deliveryInfo[0].deliveryStatus],
        messageId
      })
    )
  }
}

/**
 * Saga for receiving an incoming SMS message.
 * @method handleIncomingSMS
 * @param  {Object} action A `NOTIFICATION_RECEIVED` action representing an inbound SMS.
 */
// TODO: Have a single "handleIncoming" saga that handles both IM and SMS?
export function * handleIncomingSMS (action) {
  const notification = action.payload.inboundSMSMessageNotification
  const { message, messageId, senderAddress } = notification.inboundSMSMessage
  const { dateTime } = notification

  const existingConversation = yield select(findConversation, [senderAddress], chatTypes.SMS)
  const newConversation = !existingConversation

  yield put(
    messageActions.messageReceived(
      [senderAddress],
      [{ type: 'text', text: message }],
      messageId,
      senderAddress,
      dateTime,
      {
        newConversation: newConversation,
        type: chatTypes.SMS
      }
    )
  )
}

/**
 * Saga that fetches a list of Chat Conversations
 * @method fetchConversations
 * @param {Object} action A 'FETCH_CONVERSATIONS' action.
 */
export function * fetchConversations (action) {
  const type = action.payload.type
  const requestInfo = yield select(getRequestInfo, platforms.CPAAS)
  const response = yield call(fetchConversationsRequest, requestInfo, type)

  if (response.error) {
    yield put(
      convoActions.fetchConversationsFinished({
        error: response.error
      })
    )
  } else {
    const chatSessions = response.chatSession.map(chatSession => {
      const { sessionId, sessionType, remoteAddress, localAddress, sessionDetails } = chatSession
      const { lastText, lastPullTime, lastMessageTime } = sessionDetails

      return {
        id: sessionId,
        destination: [remoteAddress],
        address: localAddress,
        type: chatTypes[sessionType.toUpperCase()],
        lastMessage: lastText,
        lastReceived: lastMessageTime,
        lastPull: lastPullTime,
        isTypingList: [],
        messages: []
      }
    })

    yield put(
      convoActions.fetchConversationsFinished({
        conversations: chatSessions,
        error: false
      })
    )
  }
}

/**
 * Saga that fetches a list of SMS Conversations
 * @method fetchSmsConversations
 * @param {Object} action A 'FETCH_CONVERSATIONS' action.
 */
export function * fetchSmsConversations (action) {
  const type = action.payload.type
  const requestInfo = yield select(getRequestInfo, platforms.CPAAS)
  const response = yield call(fetchSmsConversationsRequest, requestInfo, type)

  if (response.error) {
    yield put(
      convoActions.fetchConversationsFinished({
        error: response.error
      })
    )
  } else {
    const smsSessions = response.chatSession.map(smsThread => {
      const { remoteAddress, localAddress, threadDetails, resourceURL } = smsThread
      const { lastText, lastPullTime, firstMessageTime, lastMessageTime, length } = threadDetails
      // the remote & local info are at the end of a string (`.../smsmessaging/v1/<userID>/remoteAddresses/<address1>/localAddresses/<address2>`).
      const tmp = resourceURL.split('/remoteAddresses/')[1]
      const addresses = tmp.split('/localAddresses/')

      // NOTE: Just like when an SMS conversation is created, the id of that conversation is generated as follows:
      //       <source>--<destination>
      return {
        id: addresses[1] + '--' + addresses[0],
        destination: [remoteAddress],
        address: localAddress,
        type: chatTypes.SMS,
        lastMessage: lastText,
        lastReceived: lastMessageTime,
        lastPull: lastPullTime,
        isTypingList: [],
        messages: [],
        firstReceived: firstMessageTime,
        length: length
      }
    })

    yield put(
      convoActions.fetchConversationsFinished({
        conversations: smsSessions,
        error: false
      })
    )
  }
}

/**
 * Saga that fetches all chat messages for a particular conversation
 * @method fetchChatMessages
 * @param {Object} action A 'FETCH_MESSAGES' action.
 */
export function * fetchChatMessages (action) {
  const requestInfo = yield select(getRequestInfo, platforms.CPAAS)
  const response = yield call(fetchMessagesRequest, requestInfo, {
    destination: action.payload.destination[0],
    type: action.payload.type
  })

  const messageList = response.chatMessage.map(message => {
    let parts = []
    if (message.text) {
      parts = parts.concat({ type: 'text', text: message.text })
    }
    if (message.attachment) {
      parts = parts.concat(
        message.attachment.map(attachment => {
          return {
            type: 'file',
            ...attachment,
            rawURL: attachment.link,
            name: attachment.name
          }
        })
      )
    }

    return {
      parts: parts,
      sender: message.senderAddress,
      destination: message['x-destinationAddress'],
      timestamp: message.dateTime,
      messageId: message.resourceURL.split('/messages/')[1] // messageID is after /messages/ in the resourceURL
    }
  })

  if (response.error) {
    yield put(
      messageActions.fetchMessagesFinished(action.payload.destination, action.payload.type, null, response.error)
    )
  } else {
    yield put(messageActions.fetchMessagesFinished(action.payload.destination, action.payload.type, messageList, null))
  }
}

/**
 * Saga that fetches all sms messages for a particular conversation.
 * @method fetchSmsMessages
 * @param {Object} action A 'FETCH_MESSAGES' action.
 */
export function * fetchSmsMessages (action) {
  const requestInfo = yield select(getRequestInfo, platforms.CPAAS)

  let senderAddress

  if (action.payload.id) {
    // id is the current conversation id, which is  of the form: <senderAddress>--<destinationAddress>
    // We just need to extract the senderAddress.
    senderAddress = action.payload.id.split('--')[0]
  } else {
    // Maintain backwards compatibility
    const messagingConfig = yield select(getMessagingConfig)
    senderAddress = messagingConfig.smsFrom || 'default'
  }

  const response = yield call(fetchSmsMessagesRequest, requestInfo, {
    source: senderAddress,
    destination: action.payload.destination[0],
    type: action.payload.type
  })

  const messageList = response.smsMessage.map(message => {
    let parts = []
    // In the SMS use case, message.message is the actual sms message in plain text.
    if (message.message) {
      parts = parts.concat({ type: 'text', text: message.message })
    }
    if (message.attachment) {
      parts = parts.concat(
        message.attachment.map(attachment => {
          return {
            type: 'file',
            ...attachment,
            rawURL: attachment.link,
            name: attachment.name
          }
        })
      )
    }

    // the remote & local info are at the end of a string (`.../smsmessaging/v1/<userID>/remoteAddresses/<address1>/localAddresses/<address2>`).
    const tmp = message.resourceURL.split('/remoteAddresses/')[1]
    const tmp2 = tmp.split('/localAddresses/')
    const remoteAddress = tmp2[0]
    const localAddress = tmp2[1].split('/messages/')[0]

    // message.type is either outbound or inbound
    // Assume type is 'outbound'.
    // This means the senderAddress is localAddress & destinationAddress is remoteAddress.
    let senderAddress = localAddress
    let destinationAddress = remoteAddress

    if (message.type !== 'outbound') {
      // otherwise, the values are swapped
      senderAddress = remoteAddress
      destinationAddress = localAddress
    }

    return {
      parts: parts,
      status: message.status,
      type: message.type,
      sender: senderAddress,
      destination: destinationAddress,
      timestamp: message.dateTime,
      messageId: message.resourceURL.split('/messages/')[1] // messageID is after /messages/ in the resourceURL
    }
  })

  if (response.error) {
    yield put(
      messageActions.fetchMessagesFinished(action.payload.destination, action.payload.type, null, response.error)
    )
  } else {
    yield put(messageActions.fetchMessagesFinished(action.payload.destination, action.payload.type, messageList, null))
  }
}

/**
 * Creates URLs for multimedia attachments and dispatches an action to update the store with the new url
 * @param {Object} action A 'GET_IMAGE_LINKS' action.
 */
export function * getImageLinks (action) {
  const requestInfo = yield select(getRequestInfo, platforms.CPAAS)
  const { parts, destination, type, messageId } = action.payload
  const fileParts = parts.filter(part => part.type === 'file')
  const responseList = yield all(fileParts.map(filePart => call(fetchImageLinks, requestInfo, filePart.rawURL)))

  yield all(
    responseList.map((response, index) => {
      if (response.error) {
        return put(messageActions.getImageLinksFinish({ error: response.error }))
      } else {
        // create a url from the blob and save it to the store.
        const url = URL.createObjectURL(response.blob)
        return put(
          messageActions.getImageLinksFinish({
            url,
            rawURL: fileParts[index].rawURL,
            destination,
            type,
            messageId
          })
        )
      }
    })
  )
}

/**
 * Helper function to determine the session or message type from the notification.
 * @param {Object} notification
 * @param {string} notificationType
 * @return {string} resourceType
 */
function getNotificationType (notification, notificationType) {
  // determine type of resource by parsing the notification link
  const link = notification.link.find(link => link.rel === notificationType)
  let resourceType
  if (link) {
    resourceType = getResourceType(link.href)
  }
  return resourceType
}

/**
 * Helper function to determine the resource type from the resourceURL.
 * @param {string} resourceURL
 * @return {string} resourceType
 */
function getResourceType (resourceURL) {
  // determine type of resource by looking for '/group/' or '/oneToOne' in the string
  // determine if the href is for a group type resource by searching for
  // '/path-string/path-string/path-string/group/' in the link's href
  let resourceType
  if (resourceURL.search(/\/\S*\/\S*\/\S*\/group\//) !== -1) {
    resourceType = chatTypes.GROUP
    // determine if the href is for a chat type resource by searching for
    // '/path-string/path-string/path-string/oneToOne/' in the link's href
  } else if (resourceURL.search(/\/\S*\/\S*\/\S*\/oneToOne\//) !== -1) {
    resourceType = chatTypes.ONETOONE
  } else {
    log.info('Unknown chat type.')
  }
  return resourceType
}
