// groups plugin.
import {
  createRequest,
  deleteRequest,
  fetchRequest,
  addParticipantRequest,
  removeParticipantRequest,
  acceptInvitationRequest,
  rejectInvitationRequest,
  leaveGroupRequest,
  fetchRequestByGroupId
} from '../requests/groups'
import * as actions from '../../interface/actions'

// Other plugins.
import { getUserInfo, getRequestInfo } from '../../../auth/interface/selectors'
import BasicError, { groupsCodes } from '../../../errors'

// Helpers.
import { logManager } from '../../../logs'

// Constants
import { platforms } from '../../../constants'

// Libraries.
import { call, put, select } from 'redux-saga/effects'
import { remapParticipants } from '../../utils'

const log = logManager.getLogger('GROUPS')

/**
 * Create a group.
 * @method create
 * @param  {Object} action An action of type `CREATE`.
 */
export function * createGroup ({ payload }) {
  const { params } = payload
  log.info(`Creating group ${params.name}.`)

  // Verify that name has been supplied
  if (!params.name) {
    log.info('name value is mandatory.')
    yield put(
      actions.createFinish(
        {},
        new BasicError({
          code: groupsCodes.MISSING_PARAMETERS,
          message: 'Failed to create group. No name value was provided.'
        })
      )
    )
    return
  }

  // add the provided participants to the createListFinish
  let participantInvites = []
  if (params.participants) {
    participantInvites = params.participants.map(participant => {
      // TODO: Why is participant an object and not a string? The docs say it
      //    should be a string.
      return { address: participant.address, 'x-isAdmin': false }
    })
  }
  const currentUser = yield select(getUserInfo)
  const requestParams = { ...params, participants: participantInvites, currentUser }

  const requestInfo = yield select(getRequestInfo, platforms.CPAAS)
  const newGroup = yield call(createRequest, requestParams, requestInfo)
  log.debug('Received response from create group request:', newGroup)

  if (!newGroup.error) {
    yield put(actions.createFinish(newGroup))
  } else {
    yield put(actions.createFinish({}, newGroup.error))
  }
}

/**
 * Delete a group.
 * @method deleteGroup
 * @param  {Object} payload Information about the group to be deleted.
 * @return {Object}
 */
export function * deleteGroup ({ payload }) {
  const { groupId } = payload
  log.info(`Deleting group ${groupId}.`)

  // verify that a groupId was supplied
  if (!groupId) {
    log.info('groupId parameter is required for delete group operation.')
    yield put(
      actions.deleteGroupFinish({
        error: new BasicError({
          code: groupsCodes.MISSING_PARAMETERS,
          message: 'Failed to delete group. groupId parameter is required.'
        })
      })
    )
    return
  }
  const requestInfo = yield select(getRequestInfo, platforms.CPAAS)
  const deletedGroup = yield call(deleteRequest, groupId, requestInfo)
  log.debug('Received response from deleteGroup request:', deletedGroup)

  if (!deletedGroup.error) {
    yield put(actions.deleteGroupFinish({ groupId }))
  } else {
    yield put(actions.deleteGroupFinish({ error: deletedGroup.error }))
  }
}

/**
 * Add a participant to a specified group.
 * @method addParticipant
 * @param  {Object} payload Information about the group and participant.
 * @return {Object}
 */
export function * addParticipant ({ payload }) {
  const { groupId, participant } = payload
  log.info(`Adding participant ${participant} to group ${groupId}.`)

  // verify that a groupId was supplied
  if (!groupId) {
    log.info('groupId parameter is required for add participant operation.')
    yield put(
      actions.addParticipantFinish({
        error: new BasicError({
          code: groupsCodes.MISSING_PARAMETERS,
          message: 'Failed to add participant. groupId parameter is required.'
        })
      })
    )
    return
  }

  // verify that a participant was supplied
  if (!participant) {
    log.info('participant parameter is required for add participant operation.')
    yield put(
      actions.addParticipantFinish({
        error: new BasicError({
          code: groupsCodes.MISSING_PARAMETERS,
          message: 'Failed to add participant. Participant parameter is required.'
        })
      })
    )
    return
  }
  const requestInfo = yield select(getRequestInfo, platforms.CPAAS)
  const newParticipant = yield call(addParticipantRequest, payload, requestInfo)
  log.debug('Received response from addParticipant request:', newParticipant)

  if (!newParticipant.error) {
    yield put(
      actions.addParticipantFinish({
        groupId,
        participant: newParticipant
      })
    )
  } else {
    yield put(actions.addParticipantFinish({ error: newParticipant.error }))
  }
}

/**
 * Remove a participant to a group.
 * @method removeParticipant
 * @param  {Object} payload Information about the group modified.
 * @return {Object}
 */
export function * removeParticipant ({ payload }) {
  const { groupId, participant } = payload
  log.info(`Removing participant ${participant} from group ${groupId}.`)
  // verify that a groupId was supplied
  if (!groupId) {
    log.info('groupId parameter is required for remove participant operation.')
    yield put(
      actions.removeParticipantFinish({
        error: new BasicError({
          code: groupsCodes.MISSING_PARAMETERS,
          message: 'Failed to remove participant. groupId parameter is required.'
        })
      })
    )
    return
  }
  // verify that a participant was supplied
  if (!participant) {
    log.info('participant parameter is required for remove participant operation.')
    yield put(
      actions.removeParticipantFinish({
        error: new BasicError({
          code: groupsCodes.MISSING_PARAMETERS,
          message: 'Failed to remove participant. Participant parameter is required.'
        })
      })
    )
    return
  }
  const requestInfo = yield select(getRequestInfo, platforms.CPAAS)
  const response = yield call(removeParticipantRequest, payload, requestInfo)
  log.debug('Received response from removeParticipant request:', response)

  if (!response.error) {
    yield put(
      actions.removeParticipantFinish({
        groupId,
        participant
      })
    )
  } else {
    yield put(actions.removeParticipantFinish({ error: response.error }))
  }
}

/**
 * Leave a specified group.
 * @method leaveGroup
 * @param  {Object} payload Information about the group modified.
 * @return {Object}
 */
export function * leaveGroup ({ payload }) {
  const { groupId } = payload
  log.info('leaveGroup saga called: ', groupId)

  // verify that a groupId was supplied
  if (!groupId) {
    log.info('groupId parameter is required for leave group operation.')
    yield put(
      actions.leaveGroupFinish({
        error: new BasicError({
          code: groupsCodes.MISSING_PARAMETERS,
          message: 'Failed to leave group. groupId parameter is required.'
        })
      })
    )
    return
  }

  const requestInfo = yield select(getRequestInfo, platforms.CPAAS)
  const userInfo = yield select(getUserInfo, platforms.CPAAS)
  const response = yield call(leaveGroupRequest, groupId, requestInfo, userInfo)
  log.debug('Received response from leave group request:', response)

  if (!response.error) {
    yield put(actions.leaveGroupFinish({ groupId, participant: userInfo.identity }))
  } else {
    yield put(actions.leaveGroupFinish({ error: response.error }))
  }
}

/**
 * Fetches all existing groups from the server.
 * @method fetch
 */
export function * fetch () {
  log.info('Fetching groups.')
  const requestInfo = yield select(getRequestInfo, platforms.CPAAS)
  const groups = yield call(fetchRequest, requestInfo)

  if (!groups.error) {
    yield put(actions.fetchGroupsFinish({ groups }))
  } else {
    yield put(actions.fetchGroupsFinish({ error: groups.error }))
  }

  log.debug('Received response from fetch request:', groups)
}

/**
 * Accept invitation to a group.
 * @method acceptInvitation
 * @param  {Object} payload Information about the group to accept an invitation to.
 * @return {Object}
 */
export function * acceptInvitation ({ payload }) {
  log.info('accept invitation saga called: ', payload)
  const { groupId } = payload

  // verify that a groupId was supplied
  if (!groupId) {
    log.info('groupId parameter is required for accepting invitation operation.')
    yield put(
      actions.acceptInvitationFinish({
        error: new BasicError({
          code: groupsCodes.MISSING_PARAMETERS,
          message: 'Failed to accept invitation. groupId parameter is required.'
        })
      })
    )
    return
  }

  // Need to accept the invitation before you can fetch the group. Otherwise, The
  // request will fail with a 404 because you are not actually in the chat group yet.
  const requestInfo = yield select(getRequestInfo, platforms.CPAAS)
  const userInfo = yield select(getUserInfo, platforms.CPAAS)
  const response = yield call(acceptInvitationRequest, groupId, requestInfo, userInfo)

  if (response.error) {
    yield put(actions.acceptInvitationFinish({ error: response.error }))
    return
  }

  // Before accepting an invitation, we need to have the current state of
  // the group in state.
  const group = yield call(fetchRequestByGroupId, groupId, requestInfo)

  if (group.error) {
    yield put(actions.acceptInvitationFinish({ error: group.error }))
    return
  }

  if (!response.error && !group.error) {
    yield put(actions.acceptInvitationFinish({ groupId, group }))
  }
}

/**
 * Reject invitation to a group.
 * @method rejectInvitation
 * @param  {Object} payload Information about the group to reject an invitation to.
 * @param  {Object} payload.groupId the Group ID for referencing this group
 * @return {Object}
 */
export function * rejectInvitation ({ payload }) {
  log.info('reject invitation saga called: ', payload)
  const { groupId } = payload

  // verify that a groupId was supplied
  if (!groupId) {
    log.info('groupId parameter is required for rejecting invitation operation.')
    yield put(
      actions.rejectInvitationFinish({
        error: new BasicError({
          code: groupsCodes.MISSING_PARAMETERS,
          message: 'Failed to reject invitation. groupId parameter is required.'
        })
      })
    )
    return
  }

  const requestInfo = yield select(getRequestInfo, platforms.CPAAS)
  const userInfo = yield select(getUserInfo, platforms.CPAAS)
  const response = yield call(rejectInvitationRequest, groupId, requestInfo, userInfo)
  if (!response.error) {
    yield put(actions.rejectInvitationFinish({ groupId }))
  } else {
    yield put(actions.rejectInvitationFinish({ error: response.error }))
  }
}

/**
 * Handle incoming 'groupChatSessionInvitationNotification' websocket messages.
 * @method handleInvitationNotification
 * @param {Object} wsAction An Object received on the websocket
 */
export function * handleInvitationNotification (wsAction) {
  const { 'x-name': name, link, participant } = wsAction.payload.groupChatSessionInvitationNotification

  // parse the href to get the groupId and add to invitation
  const linkInfo = link.find(link => link.rel === 'groupChatSessionInformation')
  const groupId = linkInfo.href.substring(linkInfo.href.lastIndexOf('/') + 1)

  // map out resourceURL and rename x-isAdmin
  const participants = remapParticipants(participant)

  const invitation = {
    groupId,
    participants,
    name
  }
  yield put(actions.invitationReceived({ invitation }))
}

/**
 * Handle incoming 'chatParticipantStatusNotification' websocket messages.
 * @method handleParticipantStatusNotification
 * @param {Object} wsAction An Object received on the websocket
 * @param {Array}  wsAction.link A list of session information objects
 * @param {Array}  wsAction.participant a list of participants for the group
 * @param {number} wsAction.dateTime the time the notification was sent
 * @param {string} wsAction.id an ID (NOT the same as groupId) for the group
 * @param {string} wsAction['x-name'] the name of the group
 */
export function * handleParticipantStatusNotification (wsAction) {
  const notification = wsAction.payload.chatParticipantStatusNotification
  const link = notification.link.find(link => link.rel === 'groupChatSessionInformation')
  const groupId = link.href.substring(link.href.lastIndexOf('/') + 1)

  // map out resourceURL and rename x-isAdmin
  notification.participants = remapParticipants(notification.participant)
  delete notification.participant

  yield put(actions.statusNotificationReceived({ groupId, notification }))
}

/**
 * Handle incoming 'chatEventNotification' websocket messages.
 * @method handleEventNotification
 * @param {Object} wsAction An Object received on the websocket
 * @param {Array}  wsAction.link A list of session information objects
 * @param {number} wsAction.dateTime the time the notification was sent
 * @param {string} wsAction.id an ID (NOT the same as groupId) for the group
 * @param {string} wsAction['x-name'] the name of the group
 */
export function * handleEventNotification (wsAction) {
  const notification = wsAction.payload.chatEventNotification
  const link = notification.link.find(link => link.rel === 'groupChatSessionInformation')
  const groupId = link.href.substring(link.href.lastIndexOf('/') + 1)

  yield put(actions.eventNotificationReceived({ groupId, notification }))
}
