// Presence plugin.
import { api, reducer } from '../interface'
import * as sagas from './sagas'
import events from '../interface/events'
import { STATUS, ACTIVITY } from './constants'

// Other plugins.
import { mapEvents } from '../../events/interface/actions'

// Libraries.
import { put } from 'redux-saga/effects'
import { values } from 'lodash/fp'

const name = 'presence'

const capabilities = ['presence']

export default () => {
  // Add plugin-specific things to the API.
  function augmentedApi (context) {
    const newApi = api(context)
    /**
     * Possible presence status values.
     * @public
     * @static
     * @name statuses
     * @memberof presence
     * @type {Object}
     * @property {string} OPEN
     * @property {string} CLOSED
     * @example
     * const { statuses, activities } = client.presence
     * // Use the values when updating presence.
     * client.presence.update(statuses.OPEN, activities.AVAILABLE)
     */
    newApi.presence.statuses = STATUS
    /**
     * Possible presence activity values.
     * @public
     * @static
     * @name activities
     * @memberof presence
     * @type {Object}
     * @property {string} AVAILABLE
     * @property {string} BUSY
     * @property {string} LUNCH
     * @property {string} VACATION
     * @property {string} ON_THE_PHONE
     * @property {string} ACTIVITIES_UNKNOWN
     * @property {string} ACTIVITIES_OTHER
     * @example
     * const { statuses, activities } = client.presence
     * // Use the values when updating presence.
     * client.presence.update(statuses.OPEN, activities.AVAILABLE)
     */
    newApi.presence.activities = ACTIVITY
    return newApi
  }

  return {
    name,
    capabilities,
    api: augmentedApi,
    reducer,
    init: () => [put(mapEvents(events))],
    sagas: values(sagas)
  }
}
