export function setCustomParameters (id, customOptions) {
  const action = {
    type: actionTypes.SET_CUSTOM_PARAMETERS,
    payload: {
      id,
      // customOptions can either be an array (i.e., just the headers) or an object (i.e., headers and body)
      customParameters: isPlainObject(customOptions) ? customOptions.customParameters : customOptions,
      customBodies: isPlainObject(customOptions) ? customOptions.customBodies : undefined
    }
  }
  return action
}

