/**
 * Call saga index.
 * Defines which actions trigger which sagas.
 */

// Call plugin.
import * as subSagas from './subscriptions'
import * as supportSagas from './support'
import * as actionTypes from '../../interfaceNew/actionTypes'
import * as webrtcActionTypes from '../../../webrtc/interface/actionTypes'
import * as requests from '../requests/calls'
import { CALL_MEDIA_CONNECTION_STATES } from '../../constants'
import { getOptions } from '../../interfaceNew/selectors'

// Callstack plugin.
import * as establishSagas from '../../../callstack/call/establish'
