// Libraries.
import { call, select, takeEvery } from 'redux-saga/effects'

/**
 * Register the call service for subscriptions.
 * @method registerCall
 */
export function * registerCall () {
  yield registerService('call', subSagas.subscribe, subSagas.unsubscribe)
}

/**
 * Call operations.
 */

/**
 * Start an outgoing call.
 * @method createCall
 * @param {Object} deps             Dependencies to be injected.
 * @param {Object} deps.webRTC      The WebRTC stack.
 */
export function * createCall (deps) {
  yield takeEvery(actionTypes.MAKE_CALL, establishSagas.makeCall, { ...deps, requests })
}

/**
 * Sending the "ringing feedback" notification to the server.
 * @method sendRingingEntry
 * @param {Object} deps             Dependencies to be injected.
 * @param {Object} deps.webRTC      The WebRTC stack.
 */
export function * sendRingingEntry (deps) {
  // Curry the signaling function to specify the status.
  function * updateSessionRinging (...args) {
    return yield call(requests.updateSessionStatus, ...args, 'Ringing')
  }

  // Dependencies provided to the Callstack saga.
  const callDeps = {
    ...deps,
    requests: {
      // Add the wrapped request to CPaaS's set of requests.
      updateCallRinging: updateSessionRinging
    }
  }

  yield takeEvery(actionTypes.SEND_RINGING_FEEDBACK, establishSagas.sendRingingFeedback, callDeps)
}

