/**
 * Other.
 */

/**
 * After subscribing for the call service, set turn information for calls.
 * @method setTurnCredentials
 */
export function * setTurnCredentials () {
  function callSubscribePattern (action) {
    return action.type === PLUGIN_SUBSCRIPTION_FINISHED && action.payload.service === 'call' && !action.error
  }

  yield takeEvery(callSubscribePattern, supportSagas.setTurnCredentials)
}

