export function * restartIce (deps) {
  yield takeEvery(actionTypes.MEDIA_RESTART, midcallSagas.iceRestart, { ...deps, requests })
}

/**
 * Behaviour: Normalize ICE connection state changes between Plan-B
 *    and Unified-Plan PeerConnections.
 * See saga description of `normalizeIceFailure` for full context.
 * @param  {Object} deps
 */
export function * watchForMediaDisconnect (deps) {
  // Watch for Session actions indicating the ICE connection has disconnected.
  function onMediaDisconnect (action) {
    return (
      action.type === webrtcActionTypes.SESSION_ICE_CONNECTION_STATE_CHANGE &&
      action.payload.iceConnectionState === CALL_MEDIA_CONNECTION_STATES.DISCONNECTED
    )
  }

  // We only need this behaviour when using Unified-Plan.
  const options = yield select(getOptions)
  if (options.defaultPeerConfig.sdpSemantics === 'unified-plan') {
    yield takeEvery(onMediaDisconnect, normalizeIceFailure)
  }
}
