// Notification plugin.
import { api, reducer, name } from '../../notifications/interface'
import events from '../../notifications/interface/events'
import * as sagas from './sagas'

// Other plugins.
import { mapEvents } from '../../events/interface/actions'
import { update as updateConfig } from '../../config/interface/actions'

// Libraries.
import { put } from 'redux-saga/effects'
import { defaultsDeep, values } from 'lodash/fp'

// Parse and/or Validate
import { validation as v8n, parse } from '../../../src/common/validation'

/**
 * Configuration options for the notification feature.
 * @public
 * @name config.notifications
 * @memberof config
 * @instance
 * @param {Object} notifications - The notifications configuration object.
 * @param {number} [notifications.idCacheLength=100] - Default amount of event ids to remember for de-duplication purposes.
 */

/**
 * CPaaS Notifications plugin factory.
 * @method notifications
 * @param {configs.notifications} options - Configuration options for authentication.
 * @return {Object} plugin - A notifications plugin.
 */
const defaultOptions = {
  idCacheLength: 100
}
// config validation
const v8nValidation = v8n.schema({
  idCacheLength: v8n.positive()
})
const parseOptions = parse('notifications', v8nValidation)
export default function notifications (options = {}) {
  const pluginOptions = defaultsDeep(defaultOptions, options)
  parseOptions(pluginOptions)

  function * init () {
    yield put(updateConfig(pluginOptions, name))
    yield put(mapEvents(events))
  }

  const capabilities = []

  return {
    name,
    capabilities,
    init,
    api,
    reducer,
    sagas: values(sagas)
  }
}
