import * as actionTypes from '../actionTypes'

/**
 * Conversation actions.
 * Actions relating to the creation/management of conversation objects.
 */

/**
 * Creates a create conversation action. Triggered when the user creates a new conversation object.
 *
 * @method createConversation
 * @param {Array<string>} destination An array of destinations for messages created in this conversation.
 * @param {Object} options The options object can contain any keys an app may want passed along into the conversation object in the store.
 * @param {string} source An optional parameter indicating the source of any messages created in this conversation.
 *        Applies only for sms type of conversations.
 * @returns {Object} A flux standard action representing the create conversation action.
 */
export function createConversation (destination, options, source) {
  // Assumption here is that if 'source' param is passed, then 'options' must be passed to explicitly specify that this is for sms.
  if (options && options.type === 'sms' && source) {
    return {
      type: actionTypes.CREATE_CONVERSATION,
      payload: {
        id: source + '--' + destination,
        source: source,
        destination: destination,
        messages: [],
        isTypingList: [],
        ...options
      }
    }
  }

  return {
    type: actionTypes.CREATE_CONVERSATION,
    payload: {
      destination: destination,
      messages: [],
      isTypingList: [],
      ...options
    }
  }
}

/**
 * Creates a fetch conversations action. This is dispatched by the API directly.
 * @param {Object} [options] An optional configuration object to query for more specific results. If no object is passed, all conversations will be retrieved
 * @method fetchConversations
 * @returns {Object} A flux standard action representing the fetch conversations action.
 */
export function fetchConversations (options) {
  return {
    type: actionTypes.FETCH_CONVERSATIONS,
    payload: {
      ...options
    }
  }
}

/**
 * Creates a fetch conversations finished action.
 * @method fetchConversationsFinished
 * @param {Object} params
 * @param {Array<conversation>} [params.conversations] An array of conversation objects, if fetch was successful
 * @param {Object} [params.error] An error object, only included if fetchConversations implementation had an error.
 * @returns {Object} A flux standard action representing the fetch conversations finished action.
 */
export function fetchConversationsFinished ({ conversations, error }) {
  return {
    type: actionTypes.FETCH_CONVERSATIONS_FINISHED,
    payload: error || { conversations },
    error: Boolean(error)
  }
}

/**
 * Creates an update conversation action.
 *
 * @method updateConversation
 * @param {Object} conversation The conversation object
 * @param {Array<string>} conversation.destination An array of strings representing the destinations for messages that are sent from this conversation object. This property is always required, as it is the primary property by which conversations are organized in messaging plugin
 * @param {number} [conversation.id] The conversation object's corresponding ID
 * @param {string} [conversation.type] The conversation type, which is expected to be one of: "chat-oneToOne", "chat-group", "sms".
 * @returns {Object} A flux standard action representing the update conversation action.
 */
export function updateConversation (conversation) {
  return {
    type: actionTypes.UPDATE_CONVERSATION,
    payload: conversation
  }
}

/**
 * Deletes all the messages from a conversation.
 * @method deleteConversation
 * @param {string} id The unique id of the conversation being deleted.
 * @param  {Array<string>} destination An array of destinations for messages created in this conversation.
 * @param {string} type The type of conversation: can be one of "chat-oneToOne", "chat-group" or "sms".
 * @returns {Object} A flux standard action representing the delete conversation action.
 */
export function deleteConversation (id, destination, type) {
  return {
    type: actionTypes.DELETE_CONVERSATION,
    payload: {
      id: id,
      destination: destination,
      type: type
    }
  }
}

/**
 * Creates a delete conversations finished action.
 * @method deleteConversationFinish
 * @param {Object} params
 * @param {Array<string>} params.destination An array of destinations for messages created in this conversation.
 * @param {string} params.type The type of conversation: can be one of "chat-oneToOne", "chat-group" or "sms".
 * @param {Object} [params.error] An error object, only present if an error occurred.
 * @returns {Object} A flux standard action representing the delete conversation finished action.
 */
export function deleteConversationFinish ({ destination, type, error }) {
  return {
    type: actionTypes.DELETE_CONVERSATION_FINISH,
    payload: error || { destination, type },
    error: !!error
  }
}

/**
 *
 * @param {Object} params
 * @param {string} params.state (active/idle) state of the typing user
 * @param {string} params.destination the target user to send the notification to
 * @returns {Object} A flux standard action representing the setIsTyping action.
 */
export function setIsTyping ({ state, destination, type }) {
  return {
    type: actionTypes.SET_IS_TYPING,
    payload: { state, destination, type }
  }
}

/**
 *
 * @param {Object} params
 * @param {string} params.state (active/idle) state of the typing user
 * @param {string} params.senderAddress the username of the user who sent the notification
 * @param {string} params.destination the target user to send the notification to
 * @param {string} params.type a standard error object
 * @param {BasicError} params.error a standard error object
 * @returns {Object} A flux standard action representing the setIsTyping finished action.
 */
export function setIsTypingFinished ({ state, senderAddress, destination, type, error }) {
  return {
    type: actionTypes.SET_IS_TYPING_FINISHED,
    payload: error || { state, senderAddress, destination, type },
    error: !!error
  }
}
