import * as messagingSagas from './messaging'
import * as subsSagas from './subscriptions'
import * as actionTypes from '../interface/actionTypes'
import { NOTIFICATION_RECEIVED } from '../../../notifications/interface/actionTypes'
import { platforms } from '../../../constants'
import { registerService } from '../../../subscription/interface/effects'
import { chatTypes } from '../../mappings'

// Libraries.
import { takeEvery } from 'redux-saga/effects'
import { get, has } from 'lodash/fp'

/**
 * Register For Services
 */

/**
 * Saga to register the chat service with the subscription plugin.
 * Runs immediately.
 * @method registerChat
 */
export function * registerChat () {
  yield registerService('chat', subsSagas.chatSubscription, subsSagas.chatUnsubscription)
}

/**
 * Saga to register the SMS service with the subscription plugin.
 * Runs immediately.
 * @method registerInboundSMS
 */
export function * registerInboundSMS () {
  yield registerService('smsinbound', subsSagas.smsInboundSubscription, subsSagas.smsInboundUnsubscription)
}

/**
 * Saga to register the SMS service with the subscription plugin.
 * Runs immediately.
 * @method registerOutboundSMS
 */
export function * registerOutboundSMS () {
  /*
   * smsoutbound subscriptions are disabled because they are not supported by
   *    the backend yet.
   * See: KAA-1925
   */
  // yield registerService('smsoutbound', subsSagas.smsOutboundSubscription, subsSagas.smsOutboundUnsubscription)
}

/**
 * Chat Operations
 */

/**
 * Send an outgoing Simple Chat Message
 * @method sendChatMessage
 */
export function * sendChatMessage () {
  function sendChatPattern (action) {
    return (
      action.type === actionTypes.SEND_MESSAGE &&
      (action.payload.message.type === chatTypes.ONETOONE || action.payload.message.type === chatTypes.GROUP)
    )
  }

  yield takeEvery(sendChatPattern, messagingSagas.sendChatMessage)
}

/**
 * Delete a Conversation
 * @method deleteConversation
 */
export function * deleteConversation () {
  yield takeEvery(actionTypes.DELETE_CONVERSATION, messagingSagas.deleteConversation)
}

/**
 * Delete a Message
 * @method deleteMessage
 */
export function * deleteMessage () {
  yield takeEvery(actionTypes.DELETE_MESSAGE, messagingSagas.deleteMessage)
}

/**
 * Handle an incoming Simple Chat Message
 * @method receiveChatMessage
 */
export function * receiveChatMessageNotification () {
  function incomingChatMessagePattern (action) {
    return (
      action.type === NOTIFICATION_RECEIVED &&
      action.payload.chatMessageNotification &&
      action.payload.chatMessageNotification.chatMessage &&
      action.payload.chatMessageNotification.chatMessage['x-type'] === 'inbound'
    )
  }

  yield takeEvery(incomingChatMessagePattern, messagingSagas.handleChatMessageNotification)
}

/**
 * SMS Operations
 */

/**
 * Waits for SMS send message actions and triggers sendSMS saga
 * @method sendSMS
 */
export function * sendSMS () {
  function sendSMSPattern (action) {
    return action.type === actionTypes.SEND_MESSAGE && action.payload.message.type === chatTypes.SMS
  }

  yield takeEvery(sendSMSPattern, messagingSagas.sendSMS)
}

/**
 * Waits for incoming SMS notification actions and triggers handleIncomingSMS saga
 * @method receiveSMS
 */
export function * receiveSMS () {
  // Redux-saga take() pattern.
  function incomingSMSPattern (action) {
    return action.type === NOTIFICATION_RECEIVED && action.payload.inboundSMSMessageNotification
  }

  yield takeEvery(incomingSMSPattern, messagingSagas.handleIncomingSMS)
}

/**
 * Waits for delivery receipts notification actions and triggers handleDeliveryReceipts saga
 * @method receiveDeliveryReceipt
 */
export function * receiveDeliveryReceipt () {
  function deliveryReceiptPattern (action) {
    return (
      action.type === NOTIFICATION_RECEIVED &&
      action.meta.platform === platforms.CPAAS &&
      has('payload.deliveryInfoNotification.deliveryInfo.deliveryStatus', action)
    )
  }

  yield takeEvery(deliveryReceiptPattern, messagingSagas.handleDeliveryReceipts)
}

/**
 * Waits for fetch conversation actions and depending on the action.payload.type, it triggers
 * fetchConversations saga (for fetching one-on-one/group conversations) or
 * fetchSmsConversations saga (for fetching sms conversations)
 * @method fetchConversations
 */
export function * fetchConversations () {
  function fetchConversationPattern (action) {
    return action.type === actionTypes.FETCH_CONVERSATIONS && action.payload.type !== 'sms'
  }

  function fetchSmsConversationPattern (action) {
    return action.type === actionTypes.FETCH_CONVERSATIONS && action.payload.type === 'sms'
  }

  yield takeEvery(fetchConversationPattern, messagingSagas.fetchConversations)

  yield takeEvery(fetchSmsConversationPattern, messagingSagas.fetchSmsConversations)
}

/**
 * Waits for fetch message actions and triggers fetchChatMessages saga or fetchSmsMessages,
 * depending of the type specified in the action's payload.
 * @method fetchMessages
 */
export function * fetchMessages () {
  function fetchChatMessagesPattern (action) {
    return action.type === actionTypes.FETCH_MESSAGES && action.payload.type !== 'sms'
  }

  function fetchSmsMessagesPattern (action) {
    return action.type === actionTypes.FETCH_MESSAGES && action.payload.type === 'sms'
  }
  yield takeEvery(fetchChatMessagesPattern, messagingSagas.fetchChatMessages)

  yield takeEvery(fetchSmsMessagesPattern, messagingSagas.fetchSmsMessages)
}

/**
 * Waits for isTyping notifications and triggers up[dateIsTyping saga
 * @method setIsTyping
 */
export function * setIsTyping () {
  function isTypingPattern (action) {
    return action.type === actionTypes.SET_IS_TYPING
  }
  yield takeEvery(isTypingPattern, messagingSagas.setIsTyping)
}

/**
 * Waits for isTyping notifications and triggers up[dateIsTyping saga
 * @method receiveIsTypingNotification
 */
export function * receiveIsTypingNotification () {
  function isTypingPattern (action) {
    return (
      action.type === NOTIFICATION_RECEIVED &&
      has('payload.chatMessageNotification.isComposing', action) &&
      get('payload.chatMessageNotification.isComposing["x-type"]', action) === 'inbound'
    )
  }
  yield takeEvery(isTypingPattern, messagingSagas.receiveIsTypingNotification)
}

/**
 * Waits for get image links notifications and triggers getImageLinks saga
 * @method getImageLinks
 */
export function * getImageLinks () {
  function getImageLinksPattern (action) {
    return action.type === actionTypes.GET_IMAGE_LINKS
  }
  yield takeEvery(getImageLinksPattern, messagingSagas.getImageLinks)
}
