// Helpers.
import request from '../../../request/effects'
import { handleRequestError } from '../../../common/helpers/handleRequestError'

// Libraries
import { logManager } from '../../../logs'
import { mapKeys, pick } from 'lodash/fp'

const log = logManager.getLogger('USERS')

/**
 * directorySearch
 *
 * Composes and executes a REST request to the directory service
 * @method directorySearch
 * @param {Object} requestInfo
 * @param {Object} params
 */
export function * directorySearch (requestInfo, params = {}) {
  const directoryId = params.directoryId || 'default'
  const method = 'GET'
  const url = `${requestInfo.baseURL}/cpaas/directory/v1/${requestInfo.username}/${directoryId}/search`
  const queryParams = mapKeys(mapSearchKey, pick(validKeys, params))

  const response = yield request({ url, queryParams, method })

  if (response.error) {
    // Handle the error similarly regardless of whether or not the response has a body value
    const error = handleRequestError(response, 'User Request')
    log.debug('Error performing directorySearch request', error)
    return {
      error: error
    }
  } else if (response.payload.body) {
    // Request was successful
    return response.payload.body.directoryItemList
  } else {
    // Request was successful but no body was provided
    return { result: true }
  }
}

/**
 * valid directory search query keys
 *
 * @type {Array}
 */
const validKeys = [
  'name',
  'firstName',
  'lastName',
  'userId',
  'userName',
  'phoneNumber',
  'order',
  'sortBy',
  'max',
  'next'
]

/**
 * Helper function to return a validated parameter for directory searches
 * @param {string} key
 * @returns {string}
 */
function mapSearchKey (key) {
  switch (key) {
    case 'userId':
      return 'userName'
    default:
      return key
  }
}
