/**
 * The messaging feature revolves around a 'conversation' namespace. It is responsible for storing the conversations
 * and its messages, and returning conversation objects when requested.
 *
 * See the "Conversation" and "Message" sections of the documentation for more details.
 *
 * Available conversations can be retrieved using the {@link conversation.get}
 *    or {@link conversation.getAll} APIs.
 *
 * Messaging functions are all part of the 'conversation' namespace. Ex: {@link messaging.getAll client.conversation.getAll}
 *
 * @public
 * @namespace conversation
 */

import { convoActions } from './actions'
import { getConversations as selectConversations, findConversation } from './selectors'
import { getSubscriptions } from '../../../subscription/interface/selectors'
import { logManager, API_LOG_TAG } from '../../../logs'
import { chatTypes, chatTypeAliases } from '../../mappings'
// Retrieve logger
const log = logManager.getLogger('Messaging')

export default function api (context) {
  const messagingApi = {
    /**
     * Fetches chat conversations that the current user is part of. This will refresh
     * the available information with any new information from the server.
     *
     * If successful, the event {@link conversation.event:conversations:change conversations:change} will be emitted.
     *
     * Available conversation information can be retrieved using the
     *    {@link conversation.get} or {@link conversation.getAll} Messaging
     *    APIs.
     *
     * @public
     * @static
     * @method fetch
     * @memberof conversation
     * @param {Object} [options] A configuration object to query for more specific results.
     * @param {string} [options.type='chat-oneToOne'] The type of conversation to fetch. See {@link conversation.chatTypes} for valid types.
     */
    fetch: function (options = {}) {
      log.debug(API_LOG_TAG + 'conversation.fetch: ', options)
      // convert chat chatTypes to CPaaS values
      if (options.type) {
        options.type = chatTypeAliases[options.type]
      }
      context.dispatch(convoActions.fetchConversations(options))
    },
    /**
     * Retrieves a conversation object matching the User ID and Type provided if available.
     *
     * Conversations are made available using the {@link conversation.fetch} or
     *    {@link conversation.create} Messaging APIs.
     *
     * @public
     * @static
     * @method get
     * @memberof conversation
     * @param {string} recipient The User ID of the remote user with which the current user had a conversation.
     * @param {Object} [options] Options used to query for more specific results.
     * @param {string} [options.type='chat-oneToOne'] The type of conversation to get. See {@link conversation.chatTypes} for valid types.
     * @returns {conversation.Conversation} A Conversation object.
     */
    get: function (recipient, options = { type: chatTypes.ONETOONE }) {
      log.debug(API_LOG_TAG + 'conversation.get: ', recipient, options)
      // convert chat chatTypes to CPaaS values
      options.type = chatTypeAliases[options.type]
      const destination = Array.isArray(recipient) ? [...recipient] : [recipient]
      let description = 'Conversation'
      let messages
      let id
      const conversation = findConversation(context.getState(), destination, options.type)
      if (conversation) {
        if (options.type === conversation.type) {
          description = conversation.description
          messages = conversation.messages
          id = conversation.id

          return context.primitives.Conversation({
            destination,
            type: options.type,
            id: id,
            description,
            messages: messages,
            isTypingList: conversation.isTypingList,
            lastReceived: conversation ? conversation.lastReceived : undefined,
            lastPull: conversation ? conversation.lastPull : undefined
          })
        } else {
          log.info(
            'Conversation found with matching destination, but conversation type does not match the type requested. Please specify the appropriate type, or call client.conversation.create() for a new type of conversation'
          )
        }
      } else {
        log.info(
          'Requested conversation not in state. Please use the create function if you would like a new conversation object'
        )
      }
      return undefined
    },
    /**
     * Creates and return a new conversation object. Any messages being sent through this Conversation
     * object will be sent to the destination provided.
     *
     * If successful, the event {@link conversation.event:conversations:new conversations:new} will be emitted.
     *
     * @public
     * @static
     * @method create
     * @memberof conversation
     * @param {string} recipient The ID of the remote user to create a conversation with. The ID will be changed to lowercase.
     * @param {Object} [options] Options to use when creating a new conversation object.
     * @param {string} [options.type='chat-oneToOne'] The type of conversation to create. See {@link conversation.chatTypes} for valid types.
     * @returns {conversation.Conversation} A Conversation object.
     */
    create: function (recipient, options = { type: chatTypes.ONETOONE }) {
      log.debug(API_LOG_TAG + 'conversation.create')
      // convert chat chatTypes to CPaaS values
      options.type = chatTypeAliases[options.type]

      const recipients = Array.isArray(recipient) ? recipient : [recipient]

      const destination = recipients.map(recipient => recipient.toLowerCase())

      const prevConv = findConversation(context.getState(), destination, options.type)

      if (!prevConv) {
        if (options.type === chatTypes.SMS) {
          // When creating an SMS conversation, provide both sender & receiver information
          // because sender can use different phone numbers to login from multiple places.
          // The sender's phone number will actually come from 'destinationAddress' of the registered subscription for 'smsinbound'.
          const subscriptions = getSubscriptions(context.getState(), 'smsinbound', 'websocket')
          let sender
          if (subscriptions.length > 0) {
            // [0]: There should be only one subscription for the given service & given channel type
            sender = subscriptions[0].destinationAddress
          } else {
            // Backwards compatibility which eventually needs to be phased out/deprecated.
            sender = context.getState().config.messaging.smsFrom
          }

          context.dispatch(convoActions.createConversation(destination, options, sender))
        } else {
          // For chat & group, receiver information is enough.
          context.dispatch(convoActions.createConversation(destination, options))
        }
      }

      return context.primitives.Conversation({
        destination,
        type: options.type
      })
    },
    /**
     * Retrieves all available conversations for the current user.
     *
     * Conversations are made available using the {@link conversation.fetch} or
     *    {@link conversation.create} Messaging APIs.
     *
     * @public
     * @static
     * @method getAll
     * @memberof conversation
     * @returns {Array<conversation.Conversation>} An array of Conversation objects.
     */
    getAll: function () {
      log.debug(API_LOG_TAG + 'conversation.getAll')
      const conversations = selectConversations(context.getState())

      // Map conversation Objects to Conversation-Mixin Objects
      return conversations.map(conversation => context.primitives.Conversation(conversation))
    },

    /**
     * Possible types of conversations.
     * @public
     * @static
     * @typedef {Object} chatTypes
     * @memberof conversation
     * @type {Object}
     * @property {string} ONETOONE A simple chat between two users.
     * @property {string} GROUP A chat between groups of users
     * @property {string} SMS A chat using SMS.
     * @example
     * // Use the chat types to fetch group conversations.
     * client.conversation.fetch({type: client.conversation.chatTypes.GROUP}) {
     */
    chatTypes
  }
  return { conversation: messagingApi }
}
