import * as actionTypes from '../actionTypes'
import { chatTypes } from '../../../mappings'

/**
 * Action helper functions.
 */

function sendMessageHelper (actionType, destination, parts, timestamp, type, id) {
  return {
    type: actionType,
    payload: {
      destination,
      id: id,
      message: {
        timestamp,
        isPending: true,
        read: false,
        parts,
        type
      }
    }
  }
}

function messageReadHelper (actionType, messageId, participant) {
  return {
    type: actionType,
    payload: { messageId, participant }
  }
}

/**
 * Message actions.
 * Actions about the CRUD of message objects.
 */

/**
 * An action fired when a delivery receipt is received
 *
 * @method deliveryReceiptReceived
 * @param {string} messageId the id of the sent message
 * @param {string} deliveryStatus the status of sent message
 * @param {Array<string>} destination An array of destinations for messages created in this conversation.
 * @param {string} type Type of message. 'chat-oneToOne', 'chat-group' or 'sms'.
 * @returns {Object} A flux standard action representing the send message action.
 */
export const deliveryReceiptReceived = (messageId, deliveryStatus, destination, type) => {
  return {
    type: actionTypes.RECEIVE_DELIVERY_RECEIPT,
    payload: { messageId, deliveryStatus, destination, type }
  }
}

/**
 * Creates a send message action. Triggered when the user initiates the send message process.
 *
 * @method sendMessage
 * @param {Array<string>} destination An array of destinations for messages created in this conversation.
 * @param {Array<part>} parts The message parts, as a formatted object.
 * @param {number} timestamp A timestamp for the sent message.
 * @param {string} type Type of message. 'chat-oneToOne', 'chat-group' or 'sms'.
 * @param {number} id The ID of the conversation as it exists in the back end.
 * @returns {Object} A flux standard action representing the send message action.
 */
export const sendMessage = (destination, parts, timestamp, type, id) =>
  sendMessageHelper(actionTypes.SEND_MESSAGE, destination, parts, timestamp, type, id)

/**
 * Creates a send message finished action. Trigged when a message send function has received a success response.
 *
 * @method sendMessageFinish
 * @param {Object} params
 * @param {Array<string>} params.destination An array of destinations for messages created in this conversation.
 * @param {string} params.sender The sender of the outgoing message.
 * @param {string} params.type The type of conversation: can be one of 'chat-oneToOne', 'chat-group' or 'sms'
 * @param {Array<part>} params.parts The message parts.
 * @param {number} params.timestamp A timestamp for the sent message.
 * @param {string} [params.messageId] The returned messageId of the message if sent successfully.
 * @param {string} [params.deliveryStatus] The status of sent message
 * @param {Object} [params.error] A basic error object
 * @returns {Object} A flux standard action representing the send message finished action.
 */
export const sendMessageFinish = ({
  destination,
  sender,
  type,
  parts,
  timestamp,
  messageId,
  deliveryStatus,
  error
}) => {
  return {
    type: actionTypes.SEND_MESSAGE_FINISH,
    payload: { destination, sender, type, parts, timestamp, messageId, deliveryStatus, error },
    error: Boolean(error)
  }
}

/**
 * Creates a message received action. Triggered when the websocket receives a chat message.
 *
 * @method messageReceived
 * @param {Array<string>} destination An array of destinations for messages created in this conversation.
 * @param {Array<part>} parts The message parts.
 * @param {string} messageId The messageId of the message that has been received.
 * @param {string} sender The user who sent the message. This is the user who the conversation is with.
 * @param {number} timestamp A timestamp for the sent message.
 * @param {Object} meta - A meta object.
 * @param {string} meta.type The type of conversation: can be one of "chat-oneToOne", "chat-group" or "sms".
 * @param {boolean} meta.newConversation - A boolean value indicating whether the message corresponds to a conversation not yet in the store.
 * @returns {Object} A flux standard action representing the message received action.
 */
export function messageReceived (
  destination,
  parts,
  messageId,
  sender,
  timestamp,
  meta = { type: chatTypes.ONETOONE, newConversation: false }
) {
  return {
    type: actionTypes.MESSAGE_RECEIVED,
    meta: meta,
    payload: {
      destination: destination,
      message: {
        timestamp: timestamp,
        isPending: false,
        read: false,
        parts: parts,
        sender: sender,
        messageId: messageId
      }
    }
  }
}

/**
 * Creates an incoming message read action. This triggers when we receive a "Message Read" notification over the websocket.
 *
 * @method incomingMessageRead
 * @param {string} messageId The unique id of the message being marked as read.
 * @param {string} participant The other participant of the conversation.
 * @returns {Object} A flux standard action representing the incoming message read action.
 */
export const incomingMessageRead = (messageId, participant) =>
  messageReadHelper(actionTypes.INCOMING_MESSAGE_READ, messageId, participant)

/**
 * Creates a send message read action. This should send a "mark message as read" request to the server.
 *
 * @method sendMessageRead
 * @param {string} messageId The unique id of the message being marked as read.
 * @param {string} participant The other participant of the conversation.
 * @returns {Object} A flux standard action representing the send message read action.
 */
export const sendMessageRead = (messageId, participant) =>
  messageReadHelper(actionTypes.SEND_MESSAGE_READ, messageId, participant)

/**
 * Creates a send message read finish action. This triggers on server response when attempting to mark a message read.
 *
 * @method sendMessageReadFinish
 * @param {Object} params
 * @param {string} params.messageId The unique id of the message being marked as read.
 * @param {string} params.participant The other participant of the conversation.
 * @param {Object} params.error A Basic error object.
 * @returns {Object} A flux standard action representing the send message read finish action.
 */
export function sendMessageReadFinish ({ messageId, participant, error }) {
  return {
    type: actionTypes.SEND_MESSAGE_READ_FINISH,
    payload: error || { messageId, participant },
    error: Boolean(error)
  }
}

/**
 * Creates a fetch messages action. This is dispatched by the API directly.
 * @method fetchMessages
 * @param {string} id The ID of the conversation whose messages are about to be fetched.
 * @param {Array<string>} destination An array of destinations for messages created in this conversation.
 * @param {number} amount A number representing the amount of messages to fetch.
 * @param {string} type The type of conversation: can be one of "chat-oneToOne", "chat-group" or "sms".
 * @returns {Object} A flux standard action representing the fetch messages action.
 */
export function fetchMessages (id, destination, amount, type) {
  return {
    type: actionTypes.FETCH_MESSAGES,
    payload: { id, destination, amount, type }
  }
}

/**
 * Creates a fetch messages finished action.
 * @method fetchMessagesFinished
 * @param {Array<string>} destination An array of destinations for messages created in this conversation.
 * @param {string} type The type of conversation: can be one of "chat-oneToOne", "chat-group" or "sms".
 * @param {Array<message>} messages An array of formatted messages to put into the store.
 * @param {Object} [error] An error object, only present if an error occurred.
 * @returns {Object} A flux standard action representing the fetch messages finished action.
 */
export function fetchMessagesFinished (destination, type, messages, error) {
  return {
    type: actionTypes.FETCH_MESSAGES_FINISHED,
    payload: error || { destination, type, messages },
    error: !!error
  }
}

/**
 * Request to clear messages from a conversation's state.
 * @method clearMessages
 * @param  {string} destination The destination for messages created in this conversation.
 * @param {string} type The type of conversation: can be one of 'chat-OneToOne', 'chat-group' or 'sms'.
 * @returns {Object} A flux standard action.
 */
export function clearMessages (destination, type) {
  return {
    type: actionTypes.CLEAR_MESSAGES,
    payload: {
      destination: destination,
      type: type
    }
  }
}

/**
 * Request to delete all the messages from a conversation.
 * @method deleteMessage
 * @param {string} id The conversation id whose message is in the process of being deleted.
 * @param  {string} destination The destination for messages created in this conversation.
 * @param {string} type The type of conversation: can be one of "chat-oneToOne", "chat-group" or "sms".
 * @param {string} messageId The ID of the message targeted for deletion
 * @returns {Object} A flux standard action.
 */
export function deleteMessage (id, destination, type, messageId) {
  return {
    type: actionTypes.DELETE_MESSAGE,
    payload: {
      id: id,
      destination: destination,
      type: type,
      messageId: messageId
    }
  }
}

/**
 * Creates a fetch messages finished action.
 * @method deleteMessageFinish
 * @param {Object} params
 * @param {Array} params.destination An array of destinations for messages created in this conversation.
 * @param {string} params.type The type of conversation: can be one of "chat-oneToOne", "chat-group" or "sms".
 * @param {string} params.messageId The ID of the message that was targeted for deletion
 * @param {Object} [params.error] An error object, only present if an error occurred.
 * @returns {Object} A flux standard action representing the fetch messages finished action.
 */
export function deleteMessageFinish ({ destination, type, messageId, error }) {
  return {
    type: actionTypes.DELETE_MESSAGE_FINISH,
    payload: error || { destination, type, messageId },
    error: !!error
  }
}

/**
 * Returns a get image links action
 * @method getImageLinks
 * @param {Object} params
 * @param {Array} params.parts the different parts of the message
 * @param {string} params.destination the destination address(es)
 * @param {string} params.type The type of conversation: can be one of "chat-oneToOne", "chat-group" or "sms".
 * @param {string} params.messageId id for looking up the message
 * @returns {Object}
 */
export function getImageLinks ({ parts, destination, type, messageId }) {
  return {
    type: actionTypes.GET_IMAGE_LINKS,
    payload: { parts, destination, type, messageId }
  }
}

/**
 * Returns a get image links finish action
 * @method getImageLinksFinish
 * @param {Object} params
 * @param {string} params.url the url that returns an attachment
 * @param {string} params.rawURL the url returned when we upload an image
 * @param {Array} params.destination the destination address(es)
 * @param {string} params.type The type of conversation: can be one of "chat-oneToOne", "chat-group" or "sms".
 * @param {string} params.messageId id for looking up the message
 * @param {Object} [params.error] a standard error object
 * @returns {Object}
 */
export function getImageLinksFinish ({ url, rawURL, destination, type, messageId, error }) {
  return {
    type: actionTypes.GET_IMAGE_LINKS_FINISH,
    payload: error || { url, rawURL, destination, type, messageId },
    error: !!error
  }
}
