// Presence plugin.
import { publish, retrievePresence, addUser, removeUser } from '../requests/presence'
import * as actions from '../../interface/actions'
import { getAllPresence } from '../../interface/selectors'

// Other plugins.
import { logManager } from '../../../logs'
import { getRequestInfo } from '../../../auth/interface/selectors'
import { getNotificationChannels } from '../../../subscription/interface/selectors'

// Helpers.
import { platforms } from '../../../constants'
import { STATUS, ACTIVITY } from '../constants'
import BasicError, { presenceCodes, subscriptionCodes } from '../../../errors'

// Libraries.
import { call, put, select } from 'redux-saga/effects'

const log = logManager.getLogger('PRESENCE')

/**
 * Map of old presence activity values to new values.
 * @type {Object}
 */
const activityMapUcToCPaaS = {
  active: ACTIVITY.AVAILABLE,
  idle: ACTIVITY.BUSY,
  away: ACTIVITY.BUSY,
  lunch: ACTIVITY.LUNCH,
  other: ACTIVITY.ACTIVITIES_OTHER,
  busy: ACTIVITY.BUSY,
  vacation: ACTIVITY.VACATION,
  'on-the-phone': ACTIVITY.ON_THE_PHONE,
  unknown: ACTIVITY.ACTIVITIES_UNKNOWN
}

/*
 * Map of old presence status values to new values.
 * @type {Object}
 */
const statusMapUcToCPaaS = {
  open: STATUS.OPEN,
  closed: STATUS.CLOSED
}

/**
 * Update presence.
 * @method updatePresence
 * @param  {Object} action An action of type `PUBLISH_PRESENCE`.
 */
export function * updatePresence ({ payload }) {
  // Convert the status to the CPaaS value (if required)
  if (payload.status in statusMapUcToCPaaS) {
    payload.status = statusMapUcToCPaaS[payload.status]
  }

  // Convert the activity to the CPaaS value (if required)
  if (payload.activity in activityMapUcToCPaaS) {
    payload.activity = activityMapUcToCPaaS[payload.activity]
  }

  // Verify that the status value is a valid CPaaS values
  if (Object.values(STATUS).indexOf(payload.status) === -1) {
    log.info('Could not update presence: Invalid status.')
    yield put(
      actions.updatePresenceFinish(
        new BasicError({
          code: presenceCodes.INVALID_STATUS,
          message: 'Invalid status in presence update request.'
        })
      )
    )
    return
  }

  // Verify that the activity value is a valid CPaaS value
  if (Object.values(ACTIVITY).indexOf(payload.activity) === -1) {
    log.info('Could not update presence: Invalid activity.')
    yield put(
      actions.updatePresenceFinish(
        new BasicError({
          code: presenceCodes.INVALID_ACTIVITY,
          message: 'Invalid activity in presence update request.'
        })
      )
    )
    return
  }

  const requestInfo = yield select(getRequestInfo, platforms.CPAAS)
  const response = yield call(publish, payload, requestInfo)
  log.debug('Received response from getRequestInfo request:', response)

  if (response instanceof BasicError) {
    yield put(actions.updatePresenceFinish(response))
  } else {
    const presenceValues = {}
    presenceValues.status = response.presence.person['overriding-willingness'].overridingWillingnessValue
    presenceValues.activity = response.presence.person.activities.activityValue
    presenceValues.note = response.presence.person.activities.other

    yield put(actions.updatePresenceFinish(presenceValues))
  }
}

/**
 * fetchPresence.
 * @method fetchPresence
 * @param  {Array<string>} users An array of userIds.
 */
export function * fetchPresence ({ payload }) {
  const users = payload
  const requestInfo = yield select(getRequestInfo, platforms.CPAAS)

  const response = yield call(retrievePresence, users, requestInfo)
  log.debug('Received response from retrievePresence request:', response)

  if (response instanceof BasicError) {
    yield put(actions.getPresenceFinish(response))
  } else {
    const presence = {}
    const presenceContact = response.presenceContact[0]
    presence.userId = presenceContact.presentityUserId
    if (presenceContact.presence) {
      presence.activity = presenceContact.presence.person.activities.activityValue
      presence.status = presenceContact.presence.person['overriding-willingness'].overridingWillingnessValue
      presence.note = presenceContact.presence.person.activities.other
    }
    yield put(actions.getPresenceFinish(presence))
  }
}

/**
 * subscribePresence.
 * @method subscribePresence
 * @param  {string} userId userId
 */
export function * subscribePresence ({ payload }) {
  const userId = payload
  const requestInfo = yield select(getRequestInfo, platforms.CPAAS)

  // get current presenceListId
  const subscriptions = yield select(getNotificationChannels)
  var presenceSubscription = subscriptions.subscriptions.filter(function (sub) {
    return sub.service === 'presence'
  })
  let response
  let action
  if (presenceSubscription.length === 0) {
    const error = new BasicError({
      message: 'Found no subscription for presence service. Subscribe for presence first and then try again.',
      code: subscriptionCodes.CPAAS_SERVICE_SUB_FAIL
    })
    error.presentityUserId = [userId]
    action = actions.subscribePresenceFinish(error)
  } else {
    const presenceListId = presenceSubscription[0].presenceListId
    response = yield call(addUser, userId, presenceListId, requestInfo)
    log.debug('Received response from addUser request:', response)
    if (response instanceof BasicError) {
      response.presentityUserId = [userId]
    }
    action = actions.subscribePresenceFinish(response)
  }

  yield put(action)
}
/**
 * unsubscribePresence.
 * @method unsubscribePresence
 * @param  {string} userId userId
 */
export function * unsubscribePresence ({ payload }) {
  const userId = payload
  const requestInfo = yield select(getRequestInfo, platforms.CPAAS)

  // get current presenceListId
  const subscriptions = yield select(getNotificationChannels)
  var presenceSubscription = subscriptions.subscriptions.filter(function (sub) {
    return sub.service === 'presence'
  })
  let response
  let action
  if (presenceSubscription.length === 0) {
    const error = new BasicError({
      message: 'Found no subscription for presence service.',
      code: subscriptionCodes.CPAAS_SERVICE_UNSUB_FAIL
    })
    error.presentityUserId = [userId]
    action = actions.unsubscribePresenceFinish(error)
  } else {
    const presenceListId = presenceSubscription[0].presenceListId
    response = yield call(removeUser, userId, presenceListId, requestInfo)
    log.debug('Received response from removeUser request:', response)
    if (response instanceof BasicError) {
      response.presentityUserId = [userId]
    }
    action = actions.unsubscribePresenceFinish(response)
  }

  yield put(action)
}

/**
 * handle incoming presence notification.
 * @method handleIncomingPresence
 * @param  {string} userId userId
 */
export function * handleIncomingPresence (wsAction) {
  const notification = wsAction.payload.presenceNotification
  const users = yield select(getAllPresence)

  if (
    notification.presence &&
    users[notification.presentityUserId] &&
    !users[notification.presentityUserId].isPending
  ) {
    const presence = {}
    presence.userId = notification.presentityUserId
    presence.activity = notification.presence.person.activities.activityValue
    presence.status = notification.presence.person['overriding-willingness'].overridingWillingnessValue
    presence.note = notification.presence.person.activities.other
    yield put(actions.presenceReceived(presence))
  }
}
