// Notification plugin.
import * as actions from '../../notifications/interface/actions'
import * as actionTypes from '../../notifications/interface/actionTypes'
import { getNotificationsInfo, getNotificationConfig } from '../../notifications/interface/selectors'

// Logs
import { logManager } from '../../logs'
import { put, select, take } from 'redux-saga/effects'

// Get the logger
const log = logManager.getLogger('NOTIFICATIONS')

/**
 * Tries to find the id of the notification.
 *
 * CPaaS notifications do not have a consistent path to their ID.
 * The ID is one level down in the object, but we need to dynamically
 * find the key that its in.
 *
 * @param {Object} notification
 * @returns The notification id if it finds one, undefined otherwise.
 */
function findNotificationInfo (notification) {
  for (const key in notification) {
    if (notification[key].id) {
      return [notification[key].id, key]
    }
  }
}

export function * processNotifications () {
  const idCache = []

  /**
   * Checks whether a notification has already been received,
   *      judging by its event ID.
   * @method isDuplicate
   * @param  {string} id
   * @param {Object} config
   * @return {boolean}
   */
  function isDuplicate (id, config) {
    // Get the list of "relevant" IDs in the queue ("relevant" being the latest X elements).
    // This covers edge-cases where the cache length config is changed (to a smaller number)
    //    after the queue has begun to be populated.
    const ids = config.idCacheLength === 0 ? [] : idCache.slice(config.idCacheLength * -1)
    // If the notification ID is part of the list, then it's a duplicate.
    if (ids.indexOf(id) !== -1) {
      return true // duplicate.
    } else {
      // Not a duplicate.
      idCache.push(id)
      while (idCache.length > config.idCacheLength) {
        idCache.shift()
      }

      return false
    }
  }

  while (true) {
    const { payload: notification, meta } = yield take(actionTypes.PROCESS_NOTIFICATION)
    const config = yield select(getNotificationConfig)
    log.info(`Received notification on channel ${meta.channel}; handling.`)

    // Only process notifications from enabled channels, ie. "silence" the channel.
    const channel = yield select(getNotificationsInfo, meta.channel)
    if (!channel.channelEnabled) {
      log.debug('Notification received on disabled channel. Ignoring it.', meta.channel)
      continue
    }

    // Find the unique ID of the received notification.
    const [notificationId, notificationType] = findNotificationInfo(notification) || []
    if (!notificationId) {
      log.error('Received notification without a findable ID.', Object.keys(notification))
      continue
    }

    log.debug(`Received notification of type ${notificationType} with ID ${notificationId}.`)
    if (!isDuplicate(notificationId, config)) {
      log.info('Notifying other plugins about notification ...')
      // Notify others (plugins) that a new notification was receive.
      yield put(actions.notificationReceived(notification, meta.platform))
      log.debug(`Finished processing notification of type ${notificationType} with ID ${notificationId}`)

      // Finish processing the notification.
      // TODO: Investigate if we need to add a handler for this action, as currently there is no handling of the action.
      yield put(actions.processNotificationFinish(notification))
    } else {
      log.info('Notification was a duplicate; ignoring ...')
      const error = new Error(`Notification id ${notificationId} is duplicate.`)
      yield put(actions.processNotificationFinish(error))
    }
  }
}
