// Users plugin.
import pluginInterface from '../interface/index'
import * as sagas from './sagas'
import contactsEvents from '../interface/events/contacts'
import usersEvents from '../interface/events/users'

// Other plugins.
import { mapEvents } from '../../events/interface/actions'

// Libraries.
import { put } from 'redux-saga/effects'

const capabilities = ['addContactAsBuddy', 'selfInfoAsUserSearch', 'advancedSearch']

export default function cpaasUsers () {
  function * init () {
    yield put(mapEvents({ ...contactsEvents, ...usersEvents }))
  }

  return {
    name: pluginInterface.name,
    sagas: Object.values(sagas),
    api: pluginInterface.api,
    reducer: pluginInterface.reducer,
    capabilities,
    init
  }
}
