// Users plugin
import * as actions from '../../interface/actions/users'
import { directorySearch } from '../requests/users'

// Other plugins.
import { getIdentity, getRequestInfo } from '../../../auth/interface/selectors'
import { logManager } from '../../../logs'

// Helpers
import BasicError from '../../../errors'
import { usersCodes } from '../../../errors/codes'

// Libraries
import { call, put, select } from 'redux-saga/effects'
import { localUserFromRemote } from '../utils'

// Constants
import { platforms } from '../../../constants'

const log = logManager.getLogger('Users')

/**
 * Search the directory for users
 * @method searchDirectory
 * @param {Object} action an action of type SEARCH_DIRECTORY
 */
export function * searchDirectory (action) {
  const { filters, options } = action.payload
  const requestInfo = yield select(getRequestInfo, platforms.CPAAS)
  const res = yield call(directorySearch, requestInfo, { ...filters, ...options })
  if (res.error) {
    const error = new BasicError({
      code: usersCodes.DIRECTORY_REQUEST_FAIL,
      message: res.error.message
    })
    yield put(actions.searchDirectoryFinish({ error }))
  } else {
    const users = res.directoryItem.map(localUserFromRemote)
    yield put(actions.searchDirectoryFinish({ users }))
  }
}

/**
 * fetch one user by their userId
 * @method fetchUser
 * @param {Object} action an action of type FETCH_USER
 */
export function * fetchUser (action) {
  const requestInfo = yield select(getRequestInfo, platforms.CPAAS)
  const res = yield call(directorySearch, requestInfo, { userId: action.payload, sortBy: 'name', order: 'asc' })
  if (res.error) {
    const error = new BasicError({
      code: usersCodes.DIRECTORY_REQUEST_FAIL,
      message: 'Directory search request resulted in an error being returned from the server'
    })
    yield put(actions.fetchUserFinish({ error }))
  } else {
    if (res.directoryItem.length > 0) {
      if (res.directoryItem.length > 1) {
        log.info('Fetch User request returned more than 1 user', res.directoryItem)
      }
      yield put(actions.fetchUserFinish(localUserFromRemote(res.directoryItem[0])))
    }
  }
}

/**
 * Fetch the user information from the directory for the current user.
 * This API is simply a shortcut for the {@link user.fetch user.fetch(getUserInfo().identity)} API.
 *
 * @method fetchSelfInfo
 * @param {Object} action an action of type FETCH_SELF_INFO
 */
export function * fetchSelfInfo (action) {
  const requestInfo = yield select(getRequestInfo, platforms.CPAAS)
  const identity = yield select(getIdentity)

  const res = yield call(directorySearch, requestInfo, {
    userName: identity.split('@')[0],
    sortBy: 'name',
    order: 'asc'
  })
  if (res.error) {
    const error = new BasicError({
      code: usersCodes.DIRECTORY_REQUEST_FAIL,
      message: 'Directory search request resulted in an error being returned from the server'
    })
    yield put(actions.fetchSelfInfoFinish({ error }))
  } else {
    if (res.directoryItem.length > 0) {
      if (res.directoryItem.length > 1) {
        log.info('Fetch Self Info returned more than 1 user', res.directoryItem)
      }
      yield put(actions.fetchSelfInfoFinish(localUserFromRemote(res.directoryItem[0])))
    }
  }
}
