// Statuses we receive from the REST responses/websocket messages
export const DeliveryStatuses = {
  DeliveredToNetwork: 'Pending',
  DeliveredToTerminal: 'Delivered',
  DeliveryImpossible: 'Failed',
  Unknown: 'Unknown'
}

/**
 * Chat types
 * Chat types used in the cpaas messaging plugin
 * @name chatTypes
 */
export const chatTypes = {
  SMS: 'sms',
  ONETOONE: 'chat-oneToOne',
  GROUP: 'chat-group'
}

// Aliases for chatTypes to allow previous chat types such as 'im', and 'group'
// to be used as well as 'chat-oneToOne' and 'chat-group'.
export const chatTypeAliases = {
  sms: chatTypes.SMS,
  'chat-oneToOne': chatTypes.ONETOONE,
  im: chatTypes.ONETOONE,
  'chat-group': chatTypes.GROUP,
  group: chatTypes.GROUP
}
