// Other plugins
import { logManager } from '../../logs'

// Helpers.
import request from '../../request/effects'
import BasicError, { messagingCodes, subscriptionCodes } from '../../errors'
import { handleRequestError } from '../../common/helpers'
import { chatTypes } from '../mappings'

// Libraries.
import { cloneDeep, property } from 'lodash/fp'

const log = logManager.getLogger('MESSAGING')

/**
 * Maps values from a nested object structure to a single object for easy use
 * assumes that each object in the list has a name and value property
 * @method _marshalData
 * @param {Array<Object>} attributes an list of objects with attributes
 * @return {Object} a remapped object that is easier to work with
 */
function _marshalData (attributes) {
  return attributes.reduce((accum, current, index) => {
    accum[current.name] = attributes[index].value
    return accum
  }, {})
}

/**
 * Makes a REST request to notify other users that the user is typing
 * @method setIsTypingRequest
 * @param {Object} requestInfo
 * @param {string} destination to send the notification to
 * @param {string} state (idle/active) the state of the user typing
 * @param {string} type The type of conversation. Can be one of "chat-oneToOne" or "chat-group"
 * @param {string} [refresh='60'] a string integer representing in seconds how long before the server refreshes the state
 * @returns {Object}
 */
export function * setIsTypingRequest (requestInfo, destination, state, type, refresh = '60') {
  let url
  if (type === chatTypes.ONETOONE) {
    url = `${requestInfo.baseURL}/cpaas/chat/v1/${requestInfo.username}/oneToOne/${destination}/adhoc/messages`
  } else if (type === chatTypes.GROUP) {
    url = `${requestInfo.baseURL}/cpaas/chat/v1/${requestInfo.username}/group/${destination[0]}/messages`
  }
  const requestOptions = {
    url: url,
    method: 'POST',
    body: JSON.stringify({
      isComposing: {
        contenttype: 'text/plain',
        refresh: refresh,
        state: state,
        lastactive: Date.now()
      }
    })
  }
  const response = yield request(requestOptions)

  if (response.error) {
    return {
      error: new BasicError({
        code: messagingCodes.SET_IS_TYPING_FAIL,
        message: 'setIsTypingRequest has failed'
      })
    }
  } else {
    return {
      ...response.payload.body.isComposing,
      error: false
    }
  }
}

/**
 * Makes a REST request to a file upload API, storing images for use later
 * @method uploadFile
 * @param {Object} requestInfo data used to make the request
 * @param {Object} file contains file data to be uploaded
 */
export function * uploadFile (requestInfo, file) {
  const formData = new FormData()
  const reqInfo = cloneDeep(requestInfo)

  const placeholderData = {
    object: {
      attributes: { attribute: [] },
      flags: { flag: [] }
    }
  }
  formData.append('root-fields', JSON.stringify(placeholderData))
  formData.append('raw', file, file.name)
  const requestOptions = {
    url: `${reqInfo.baseURL}/cpaas/nms/v1/chat/${reqInfo.username}/objects`,
    body: formData,
    method: 'POST'
  }

  /*
   * A FormData request auto-populates the Content-Type header with a unique
   *    value. To prevent the request plugin from using a default value, we need
   *    to override the "common" options by passing in options specific for this
   *    request.
   */
  const manualOptions = cloneDeep(requestInfo.options)
  delete manualOptions.headers['Content-Type']

  const response = yield request(requestOptions, manualOptions)

  if (response.error) {
    return {
      error: new BasicError({
        code: messagingCodes.FILE_UPLOAD_FAIL,
        message: 'Failed to upload file'
      })
    }
  } else {
    const responseData = _marshalData(response.payload.body.object.attributes.attribute)
    return {
      attachment: responseData,
      error: false
    }
  }
}

/**
 * Used to send Simple Chat messages
 * sends a chat message to another user (can include attachments)
 * @method sendChatMessageRequest
 * @param {Object} requestInfo
 * @param {Object} payload
 * @param {Object} payload.destination a target destination for the message
 * @param {Object} payload.textParts text parts of the message
 * @param {Object} payload.fileParts parts of the message required for sending files
 * @returns {Object}
 */
export function * sendChatMessageRequest (requestInfo, destination, textParts, fileParts) {
  const url = `${requestInfo.baseURL}/cpaas/chat/${requestInfo.version}/${requestInfo.username}/oneToOne/${destination}/adhoc/messages`

  const requestOptions = {
    method: 'POST',
    url: url,
    body: JSON.stringify({
      chatMessage: {
        text: textParts,
        attachment: fileParts
      }
    })
  }

  const response = yield request(requestOptions)

  if (response.error) {
    return {
      error: handleRequestError(response, 'Send chat message')
    }
  } else {
    // Success scenario.
    return {
      ...response.payload.body.chatMessage,
      error: false
    }
  }
}

