/**
 * Used to delete chat conversation
 * @method deleteChatConversationRequest
 * @param {Object} requestInfo
 * @param {string} destination Destination associated with the conversation to delete
 * @param {string} type Type of conversation. Can be one of "chat-oneToOne" or "chat-group"
 * @returns {Object}
 */
export function * deleteChatConversationRequest (requestInfo, destination, type) {
  let url
  if (type === chatTypes.ONETOONE) {
    url = `${requestInfo.baseURL}/cpaas/chat/v1/${requestInfo.username}/oneToOne/${destination}/adhoc/messages`
  } else if (type === chatTypes.GROUP) {
    url = `${requestInfo.baseURL}/cpaas/chat/v1/${requestInfo.username}/group/${destination}`
  } else if (type === chatTypes.SMS) {
    url = `${requestInfo.baseURL}/cpaas/smsmessaging/v1/${requestInfo.username}/remoteAddresses/${destination}/localAddresses/${requestInfo.senderAddress}`
  }

  const requestOptions = {
    method: 'DELETE',
    responseType: 'none',
    url: url
  }

  const response = yield request(requestOptions)

  // a 404 error results when the conversation has no messages and we try to delete it
  // there is a mismatch between the CPaaS backend and how the SDK treats conversations.
  // See KAA-1984 for more info
  const legitError = response => response.error && property('payload.result.code')(response) !== 404

  if (legitError(response)) {
    return {
      error: handleRequestError(response, 'Delete conversation')
    }
  } else {
    // Success scenario.
    return {
      error: false
    }
  }
}

/**
 * Used to delete chat message
 * @method deleteChatMessageRequest
 * @param {Object} requestInfo
 * @param {Object} destination Destination associated with the conversation
 * @param {Object} messageId MessageId of message to delete
 * @param {Object} type Type of conversation. Can be one of "chat-oneToOne" or "chat-group" or "sms"
 * @returns {Object}
 */
export function * deleteChatMessageRequest (requestInfo, destination, messageId, type) {
  let url
  if (type === chatTypes.ONETOONE) {
    url = `${requestInfo.baseURL}/cpaas/chat/v1/${requestInfo.username}/oneToOne/${destination}/adhoc/messages/${messageId}`
  } else if (type === chatTypes.GROUP) {
    url = `${requestInfo.baseURL}/cpaas/chat/v1/${requestInfo.username}/group/${destination}/messages/${messageId}`
  } else if (type === chatTypes.SMS) {
    url = `${requestInfo.baseURL}/cpaas/smsmessaging/v1/${requestInfo.username}/remoteAddresses/${destination}/localAddresses/${requestInfo.senderAddress}/messages/${messageId}`
  }

  const requestOptions = {
    method: 'DELETE',
    responseType: 'none',
    url: url
  }

  const response = yield request(requestOptions)

  if (response.error) {
    return {
      error: handleRequestError(response, 'Delete message')
    }
  } else {
    // Success scenario.
    return {
      error: false
    }
  }
}

/**
 * Performs a REST request for a chat subscription.
 * @method chatSubscribe
 * @param  {Object} requestInfo Info needed to perform the request.
 * @param  {Object} channel
 * @return {Object}
 */
export function * chatSubscribe (requestInfo, channel) {
  const requestOptions = {}
  requestOptions.method = 'POST'
  requestOptions.url = `${requestInfo.baseURL}/cpaas/chat/${requestInfo.version}/${requestInfo.username}/subscriptions`

  requestOptions.body = JSON.stringify({
    chatNotificationSubscription: {
      callbackReference: {
        notifyURL: channel.callbackURL
      },
      clientCorrelator: requestInfo.clientCorrelator
    }
  })

  const response = yield request(requestOptions)

  if (response.error) {
    return {
      error: new BasicError({
        message: 'Failed to subscribe for chat notifications.',
        code: subscriptionCodes.CPAAS_SERVICE_SUB_FAIL
      })
    }
  } else {
    return {
      error: false,
      subscription: response.payload.body.chatNotificationSubscription
    }
  }
}

/**
 * Performs a REST request for a chat unsubscription.
 * @method chatUnsubscribe
 * @param  {Object} requestInfo Info needed to perform the request.
 * @param  {Object} subInfo Info about the subscription being ..unsubscribed.
 * @return {Object}
 */
export function * chatUnsubscribe (requestInfo, subInfo) {
  const requestOptions = {}
  requestOptions.method = 'DELETE'
  requestOptions.responseType = 'none'

  requestOptions.url =
    `${requestInfo.baseURL}/cpaas/` +
    `chat/${requestInfo.version}/${requestInfo.username}/` +
    `subscriptions/${subInfo.subscriptionId}`

  const response = yield request(requestOptions)

  if (response.error && response.payload.result.code !== 404) {
    return {
      error: new BasicError({
        message: 'Failed to unsubscribe from chat notifications.',
        code: subscriptionCodes.CPAAS_SERVICE_UNSUB_FAIL
      })
    }
  } else {
    return {
      error: false
    }
  }
}

/**
 * Makes a REST request to the 'Send SMS' CPaaS endpoint.
 * @method sendSMSRequest
 * @param  {Object} connection Information needed for the request.
 * @param  {Object} payload Information needed for the SMS.
 * @return {Object} Formatted response object.
 */
export function * sendSMSRequest (requestInfo, destination, message) {
  const requestOptions = {}
  requestOptions.method = 'POST'

  requestOptions.url =
    `${requestInfo.baseURL}` +
    `/cpaas/smsmessaging/v1/${requestInfo.username}` +
    `/outbound/${requestInfo.senderAddress}/requests`

  requestOptions.body = JSON.stringify({
    outboundSMSMessageRequest: {
      address: [destination],
      clientCorrelator: requestInfo.clientCorrelator,
      outboundSMSTextMessage: {
        message: message
      }
    }
  })

  const response = yield request(requestOptions)

  if (response.error) {
    // Request error.
    log.debug('Failed to send SMS message.', response.error)
    return {
      error: new BasicError({
        code: messagingCodes.SEND_MESSAGE_FAIL,
        message: 'Failed to send SMS message.'
      })
    }
  } else if (response.payload.error) {
    // Server error.
    log.debug('Failed to send SMS message.', response.payload.error)
    return {
      error: new BasicError({
        code: messagingCodes.SEND_MESSAGE_FAIL,
        message: 'Failed to send SMS message.'
      })
    }
  } else {
    // Success scenario.
    return {
      ...response.payload.body.outboundSMSMessageRequest,
      error: false
    }
  }
}

