// Helpers.
import request from '../../../request/effects'
import BasicError, { subscriptionCodes } from '../../../errors'

/**
 * Request saga. Generates and sends a REST request to the backend.
 * @method callSubscribe
 * @param  {Object} requestInfo
 * @param  {Object} subInfo
 * @return {Object}
 */
export function * callSubscribe (requestInfo, channelInfo) {
  const requestOptions = {
    method: 'POST',
    url:
      `${requestInfo.baseURL}/cpaas/` +
      `webrtcsignaling/${requestInfo.version}/${requestInfo.username}/` +
      'subscriptions',
    body: JSON.stringify({
      wrtcsNotificationSubscription: {
        callbackReference: {
          notifyURL: channelInfo.callbackURL
        },
        clientCorrelator: requestInfo.clientCorrelator
      }
    })
  }

  const response = yield request(requestOptions)

  if (response.error) {
    return {
      // TODO: An actual error.
      error: new BasicError({
        message: 'Failed to subscribe for call notifications',
        code: subscriptionCodes.CPAAS_SERVICE_SUB_FAIL
      })
    }
  } else {
    return {
      subscription: response.payload.body.wrtcsNotificationSubscription
    }
  }
}

/**
 * Request saga. Generates and sends a REST request to the backend.
 * @method callUnsubscribe
 * @param  {Object} requestInfo
 * @param  {Object} subInfo
 * @return {Object}
 */
export function * callUnsubscribe (requestInfo, subInfo) {
  const requestOptions = {
    method: 'DELETE',
    responseType: 'none',
    url:
      `${requestInfo.baseURL}/cpaas/` +
      `webrtcsignaling/${requestInfo.version}/${requestInfo.username}/` +
      `subscriptions/${subInfo.subscriptionId}`
  }

  const response = yield request(requestOptions)

  if (response.error && response.payload.result.code !== 404) {
    return {
      error: new BasicError({
        message: 'Failed to unsubscribe from call notifications.',
        code: subscriptionCodes.CPAAS_SERVICE_SUB_FAIL
      })
    }
  } else {
    return {
      error: false
    }
  }
}
