/**
 * REST request to retrieve presence states of requested presentities.
 * @method retrievePresence
 * @param  {Array} users Array of userId's
 * @param  {Object} requestInfo
 * @return {Object}
 */
export function * retrievePresence (users, requestInfo) {
  const adhocPresenceList = {
    adhocPresenceList: {
      presentityUserId: users
    }
  }

  const requestOptions = {
    method: 'POST',
    url: `${requestInfo.baseURL}/cpaas/presence/${requestInfo.version}/` + `${requestInfo.username}/adhocPresenceList`,
    body: JSON.stringify(adhocPresenceList)
  }

  const response = yield request(requestOptions)

  if (response.error) {
    return handleRequestError(response, 'Fetch users presence')
  } else {
    return response.payload.body.presenceList
  }
}
/**
 * REST request to add a new presentity to the presence list.
 * @method addUser
 * @param  {string} user userId
 * @param  {string} presenceListId Resource ID of the presenceList
 * @param  {Object} requestInfo
 * @return {Object}
 */
export function * addUser (userId, presenceListId, requestInfo) {
  const presenceContact = {
    presenceContact: {
      presentityUserId: userId
    }
  }

  const requestOptions = {
    method: 'PUT',
    url:
      `${requestInfo.baseURL}/cpaas/presence/${requestInfo.version}/` +
      `${requestInfo.username}/presenceLists/${presenceListId}/` +
      `presenceContacts/${userId}`,
    body: JSON.stringify(presenceContact)
  }

  const response = yield request(requestOptions)

  if (response.error) {
    return handleRequestError(response, 'Add presence user')
  } else {
    return response.payload.body.presenceContact
  }
}
/**
 * REST request to delete a presentity from a presence list.
 * @method removeUser
 * @param  {string} user userId
 * @param  {string} presenceListId Resource ID of the presenceList
 * @param  {Object} requestInfo
 * @return {Object}
 */
export function * removeUser (userId, presenceListId, requestInfo) {
  const requestOptions = {
    method: 'DELETE',
    responseType: 'none',
    url:
      `${requestInfo.baseURL}/cpaas/presence/${requestInfo.version}/` +
      `${requestInfo.username}/presenceLists/${presenceListId}/` +
      `presenceContacts/${userId}`
  }

  const response = yield request(requestOptions)

  if (response.error) {
    return handleRequestError(response, 'Remove presence user')
  } else {
    return response.payload.result
  }
}
