// Users plugin
import * as actions from '../../interface/actions/contacts'
import {
  addContactRequest,
  updateContactRequest,
  deleteContactRequest,
  fetchContactRequest,
  refreshContactsRequest
} from '../requests/contacts'

// Other plugins.
import { getRequestInfo } from '../../../auth/interface/selectors'

// Helpers
import { remoteContactFromLocal, localContactFromRemote } from '../utils'
import BasicError from '../../../errors'
import { usersCodes } from '../../../errors/codes'

// Libraries
import { call, put, select } from 'redux-saga/effects'

/**
 * Add a contact to the Address Book
 * @method addContact
 * @param {Object} action an action of type ADD_CONTACT
 */
export function * addContact (action) {
  const requestInfo = yield select(getRequestInfo, 'cpaas')
  const contactBody = remoteContactFromLocal(action.payload, true)
  const res = yield call(addContactRequest, requestInfo, JSON.stringify(contactBody))
  if (res.error) {
    yield put(actions.addContactFinish({ error: res.error }))
  } else {
    yield put(actions.addContactFinish({ contact: action.payload }))
  }
}

/**
 * Update a contact in the address book
 * @method updateContact
 * @param {Object} action an action of type UPDATE_CONTACT
 */
export function * updateContact (action) {
  // validate
  if (!action.payload.contact.contactId) {
    // contactId is a mandatory field
    yield put(
      actions.updateContactFinish({
        error: new BasicError({
          code: usersCodes.INVALID_PARAM,
          message: 'Missing property: contactId'
        })
      })
    )
    return
  }

  const requestInfo = yield select(getRequestInfo, 'cpaas')
  const contactBody = remoteContactFromLocal(action.payload.contact, false)
  const res = yield call(
    updateContactRequest,
    requestInfo,
    action.payload.contact.contactId,
    JSON.stringify(contactBody)
  )
  if (res.error) {
    yield put(actions.updateContactFinish({ error: res.error }))
  } else {
    yield put(actions.updateContactFinish({ contact: action.payload.contact }))
  }
}

/**
 * Remove a contact from the address book
 * @method removeContact
 * @param {Object} action an action of type REMOVE_CONTACT
 */
export function * removeContact (action) {
  const requestInfo = yield select(getRequestInfo, 'cpaas')
  const res = yield call(deleteContactRequest, requestInfo, action.payload)
  if (res.error) {
    yield put(actions.removeContactFinish({ error: res.error }))
  } else {
    yield put(actions.removeContactFinish({ contactId: action.payload }))
  }
}

/**
 * Fetch a contact from the personal address book of the subscribed user.
 * @method fetchContact
 * @param {Object} action an action of type FETCH_CONTACT
 */
export function * fetchContact (action) {
  const requestInfo = yield select(getRequestInfo, 'cpaas')
  const res = yield call(fetchContactRequest, requestInfo, action.payload)
  if (res.error) {
    yield put(actions.fetchContactFinish({ error: res.error }))
  } else {
    yield put(actions.fetchContactFinish({ contact: localContactFromRemote(res.result) }))
  }
}

/**
 * Refresh the state's contacts by fetching the latest contacts from the user's address book
 * @method refreshContacts
 * @param {Object} action an action of type REFRESH_CONTACTS
 */
export function * refreshContacts (action) {
  const requestInfo = yield select(getRequestInfo, 'cpaas')
  const res = yield call(refreshContactsRequest, requestInfo)
  if (res.error) {
    yield put(actions.refreshContactsFinish({ error: res.error }))
  } else {
    yield put(actions.refreshContactsFinish({ contacts: res.result.map(localContactFromRemote) }))
  }
}
