import * as actionTypes from './actionTypes'

// Libraries
import { handleActions } from 'redux-actions'
import { omit } from 'lodash/fp'

// Helpers
import { subscriptionCodes } from '../../errors'

const reducers = {}

reducers[actionTypes.UPDATE] = {
  next (state) {
    return {
      ...state,
      self: {
        ...state.self,
        loading: true,
        error: false
      }
    }
  }
}

reducers[actionTypes.UPDATE_FINISH] = {
  next (state, action) {
    const result = {
      ...state,
      self: {
        loading: false,
        error: false,
        status: action.payload.status,
        activity: action.payload.activity
      }
    }
    if (action.payload.note) {
      result.self.note = action.payload.note
    }
    return result
  },
  throw (state, action) {
    return {
      ...state,
      self: {
        ...state.self,
        loading: false,
        error: action.payload
      }
    }
  }
}

reducers[actionTypes.GET] = {
  next (state, { payload: userIds }) {
    const users = { ...state.users }
    for (const userId of userIds) {
      users[userId] = {
        userId,
        loading: true
      }
    }
    return { ...state, users }
  }
}

reducers[actionTypes.GET_FINISH] = {
  next (state, { payload }) {
    if (!payload.presenceContact) {
      return { ...state }
    }

    const users = {}
    for (const contact of payload.presenceContact) {
      const presenceObject = {}
      presenceObject.userId = contact.presentityUserId
      if (contact.presence && contact.presence.person) {
        presenceObject.activity = contact.presence.person.activities.activityValue
        presenceObject.status = contact.presence.person['overriding-willingness'].overridingWillingnessValue
        presenceObject.note = contact.presence.person.activities.other
      }
      presenceObject.loading = false
      users[contact.presentityUserId] = presenceObject
    }
    return {
      ...state,
      users: {
        ...state.users,
        ...users
      }
    }
  },
  throw (state, action) {
    return {
      ...state
    }
  }
}

reducers[actionTypes.RECEIVED] = {
  next (state, { payload }) {
    return {
      ...state,
      users: {
        ...state.users,
        [payload.userId]: {
          userId: payload.userId, // optimize for filtering by userId
          status: payload.status,
          activity: payload.activity,
          note: payload.note,
          loading: false
        }
      }
    }
  }
}

reducers[actionTypes.SUBSCRIBE] = {
  next (state, { payload }) {
    // Payload could be a string (single userId) or array (multiple userIds)
    if (!Array.isArray(payload)) {
      payload = [payload]
    }
    const updatedUsers = Object.assign({}, state.users)
    payload.forEach(userId => {
      updatedUsers[userId] = {
        userId,
        loading: false,
        isPending: true
      }
    })

    return {
      ...state,
      users: updatedUsers
    }
  }
}

reducers[actionTypes.SUBSCRIBE_FINISH] = {
  next (state, { payload }) {
    const updatedUsers = Object.assign({}, state.users)
    payload.presentityUserId.forEach(userId => {
      updatedUsers[userId] = {
        userId,
        loading: false,
        isPending: false
      }
    })

    return {
      ...state,
      users: updatedUsers
    }
  },
  throw (state, { payload }) {
    const updatedUsers = Object.assign({}, state.users)
    payload.presentityUserId.forEach(userId => {
      delete updatedUsers[userId]
    })

    return {
      ...state,
      users: updatedUsers
    }
  }
}

reducers[actionTypes.UNSUBSCRIBE] = {
  next (state, { payload }) {
    // Payload could be a string (single userId) or array (multiple userIds)
    if (!Array.isArray(payload)) {
      payload = [payload]
    }
    const updatedUsers = Object.assign({}, state.users)
    payload.forEach(userId => {
      updatedUsers[userId] = {
        userId,
        loading: false,
        isPending: true
      }
    })

    return {
      ...state,
      users: updatedUsers
    }
  }
}

reducers[actionTypes.UNSUBSCRIBE_FINISH] = {
  next (state, { payload }) {
    return {
      ...state,
      users: omit(payload.presentityUserId, state.users)
    }
  },
  throw (state, { payload }) {
    let updatedUsers = Object.assign({}, state.users)
    if (payload.code === subscriptionCodes.CPAAS_SERVICE_UNSUB_FAIL) {
      // Remove the userId from state
      updatedUsers = omit(payload.presentityUserId, updatedUsers)
    } else {
      // Update the userId in state
      if (payload.presentityUserId && payload.presentityUserId.length > 0) {
        payload.presentityUserId.forEach(userId => {
          updatedUsers[userId] = {
            userId,
            loading: false,
            isPending: false
          }
        })
      }
    }

    return {
      ...state,
      users: updatedUsers
    }
  }
}

const reducer = handleActions(reducers, { self: {}, users: {} })
export default reducer
