/**
 * Groups saga index.
 * Defines which actions trigger which sagas.
 */

// groups plugin.
import * as groupSagas from './groups'
import * as actionTypes from '../../interface/actionTypes'

// Libraries.
import { takeEvery } from 'redux-saga/effects'

// Other plugins.
import { NOTIFICATION_RECEIVED } from '../../../notifications/interface/actionTypes'

/**
 * Create group.
 * @method processCreateGroup
 */
export function * processCreateGroup () {
  yield takeEvery(actionTypes.CREATE_GROUP, groupSagas.createGroup)
}

/**
 * Delete group.
 * @method processDeleteGroup
 */
export function * processDeleteGroup () {
  yield takeEvery(actionTypes.DELETE_GROUP, groupSagas.deleteGroup)
}

/**
 * Add a participant to a group.
 * @method processAddParticipant
 */
export function * processAddParticipant () {
  yield takeEvery(actionTypes.ADD_PARTICIPANT, groupSagas.addParticipant)
}

/**
 * Remove a participant from a group.
 * @method processRemoveParticipant
 */
export function * processRemoveParticipant () {
  yield takeEvery(actionTypes.REMOVE_PARTICIPANT, groupSagas.removeParticipant)
}

/**
 * Leave group.
 * @method leaveGroup
 */
export function * leaveGroup () {
  yield takeEvery(actionTypes.LEAVE_GROUP, groupSagas.leaveGroup)
}

/**
 * Fetch groups.
 * @method fetch
 */
export function * fetch () {
  yield takeEvery(actionTypes.FETCH_GROUPS, groupSagas.fetch)
}

/**
 * Accept invitation.
 * @method acceptInvitation
 */
export function * acceptInvitation () {
  yield takeEvery(actionTypes.ACCEPT_INVITATION, groupSagas.acceptInvitation)
}

/**
 * Reject invitation.
 * @method rejectInvitation
 */
export function * rejectInvitation () {
  yield takeEvery(actionTypes.REJECT_INVITATION, groupSagas.rejectInvitation)
}

/**
 * Receive invitation. (groupChatSessionInvitationNotification)
 * @method receiveInvitationNotification
 */
export function * receiveInvitationNotification () {
  yield takeEvery(
    action => action.type === NOTIFICATION_RECEIVED && action.payload.groupChatSessionInvitationNotification,
    groupSagas.handleInvitationNotification
  )
}

/**
 * Receive participant status. (chatParticipantStatusNotification)
 * @method receiveParticipantStatusNotification
 */
export function * receiveParticipantStatusNotification () {
  yield takeEvery(
    action => action.type === NOTIFICATION_RECEIVED && action.payload.chatParticipantStatusNotification,
    groupSagas.handleParticipantStatusNotification
  )
}

/**
 * Receive chat event notification. (chatEventNotification)
 * @method receiveEventNotification
 */
export function * receiveEventNotification () {
  yield takeEvery(
    action =>
      action.type === NOTIFICATION_RECEIVED &&
      action.payload.chatEventNotification &&
      action.payload.chatEventNotification.eventType === 'SessionUpdated',
    groupSagas.handleEventNotification
  )
}
